import React, { useEffect } from 'react';
import { useAuth } from '@/contexts/AuthContext';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';

export default function UserStatusPage() {
  const { 
    user, 
    error, 
    isLoading, 
    isAuthenticated, 
    checkAuth
  } = useAuth();

  useEffect(() => {
    console.log('UserStatusPage mounted');
    console.log('Current error:', error);
    console.log('Is authenticated:', isAuthenticated);
    console.log('User:', user);
  }, [error, isAuthenticated, user]);

  const handleRefreshAuth = async () => {
    console.log('Manual refresh auth called');
    await checkAuth();
  };

  const handleGetCurrentUser = async () => {
    try {
      console.log('Calling checkAuth...');
      await checkAuth();
      console.log('checkAuth completed');
    } catch (error) {
      console.error('checkAuth error:', error);
      alert(`checkAuth error: ${error}`);
    }
  };

  return (
    <div className="container mx-auto px-4 py-8">
      <Card>
        <CardHeader>
          <CardTitle></CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="grid grid-cols-2 gap-4 text-sm">
            <div>
              <strong>:</strong> {error || ''}
            </div>
            <div>
              <strong>:</strong> {isAuthenticated ? '' : ''}
            </div>
            <div>
              <strong>:</strong> {isLoading ? '' : ''}
            </div>

          </div>

          {user && (
            <div className="border rounded p-4">
              <h3 className="font-semibold mb-2">:</h3>
              <pre className="text-xs bg-gray-100 p-2 rounded overflow-auto">
                {JSON.stringify(user, null, 2)}
              </pre>
            </div>
          )}

          <div className="flex space-x-4">
            <Button onClick={handleRefreshAuth}>
              
            </Button>
            <Button onClick={handleGetCurrentUser} variant="outline">
               checkAuth
            </Button>
            <Button 
              onClick={() => {
                localStorage.removeItem('authing_user');
                localStorage.removeItem('authing_code');
                localStorage.removeItem('authing_state');
                window.location.reload();
              }} 
              variant="destructive"
            >
              
            </Button>
          </div>

          <div className="text-xs text-gray-500">
            <p>:</p>
            <p>- </p>
            <p>- " getCurrentUser"</p>
            <p>- </p>
          </div>
        </CardContent>
      </Card>
    </div>
  );
} 