/**
 * 
 * 
 */

import React, { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { useToast } from '@/hooks/use-toast';
import { useNavigate, Link } from 'react-router-dom';
import { ArrowLeft, Phone, Mail, Lock, Send } from 'lucide-react';

/**
 * 
 * @returns React 
 */
export default function ForgotPasswordPage() {
  const [activeTab, setActiveTab] = useState('phone');
  const [isLoading, setIsLoading] = useState(false);
  const [countdown, setCountdown] = useState(0);
  const [step, setStep] = useState<'input' | 'verify' | 'reset'>('input');
  
  // 
  const [phoneForm, setPhoneForm] = useState({
    phone: '',
    code: '',
    newPassword: '',
    confirmPassword: ''
  });
  
  // 
  const [emailForm, setEmailForm] = useState({
    email: '',
    code: '',
    newPassword: '',
    confirmPassword: ''
  });
  
  const { toast } = useToast();
  const navigate = useNavigate();

  /**
   * 
   */
  const handlePhoneFormChange = (field: string, value: string) => {
    setPhoneForm(prev => ({ ...prev, [field]: value }));
  };

  /**
   * 
   */
  const handleEmailFormChange = (field: string, value: string) => {
    setEmailForm(prev => ({ ...prev, [field]: value }));
  };

  /**
   * 
   */
  const sendCode = async (type: 'phone' | 'email') => {
    if (countdown > 0) return;
    
    const value = type === 'phone' ? phoneForm.phone : emailForm.email;
    
    if (!value) {
      toast({
        title: "" + (type === 'phone' ? '' : ''),
        variant: "destructive"
      });
      return;
    }
    
    if (type === 'phone' && !/^1[3-9]\d{9}$/.test(value)) {
      toast({
        title: "",
        variant: "destructive"
      });
      return;
    }
    
    if (type === 'email' && !/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(value)) {
      toast({
        title: "",
        variant: "destructive"
      });
      return;
    }
    
    try {
      // API
      toast({
        title: "",
        description: `${type === 'phone' ? '' : ''}`
      });
      
      // 
      setCountdown(60);
      const timer = setInterval(() => {
        setCountdown(prev => {
          if (prev <= 1) {
            clearInterval(timer);
            return 0;
          }
          return prev - 1;
        });
      }, 1000);
      
      // 
      setStep('verify');
    } catch (error) {
      toast({
        title: "",
        description: "",
        variant: "destructive"
      });
    }
  };

  /**
   * 
   */
  const verifyCode = async (type: 'phone' | 'email') => {
    const form = type === 'phone' ? phoneForm : emailForm;
    
    if (!form.code) {
      toast({
        title: "",
        variant: "destructive"
      });
      return;
    }
    
    try {
      // API
      toast({
        title: "",
        description: ""
      });
      
      // 
      setStep('reset');
    } catch (error) {
      toast({
        title: "",
        description: "",
        variant: "destructive"
      });
    }
  };

  /**
   * 
   */
  const resetPassword = async (type: 'phone' | 'email') => {
    const form = type === 'phone' ? phoneForm : emailForm;
    
    if (!form.newPassword || !form.confirmPassword) {
      toast({
        title: "",
        variant: "destructive"
      });
      return;
    }
    
    if (form.newPassword !== form.confirmPassword) {
      toast({
        title: "",
        variant: "destructive"
      });
      return;
    }
    
    if (form.newPassword.length < 6) {
      toast({
        title: "",
        description: "6",
        variant: "destructive"
      });
      return;
    }
    
    setIsLoading(true);
    try {
      // API
      toast({
        title: "",
        description: ""
      });
      
      // 
      setPhoneForm({ phone: '', code: '', newPassword: '', confirmPassword: '' });
      setEmailForm({ email: '', code: '', newPassword: '', confirmPassword: '' });
      setStep('input');
      
      // 
      setTimeout(() => {
        navigate('/login');
      }, 1500);
    } catch (error: any) {
      toast({
        title: "",
        description: error.message || "",
        variant: "destructive"
      });
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-purple-50 flex items-center justify-center p-4">
      <div className="w-full max-w-md">
        {/*  */}
        <Button
          variant="ghost"
          onClick={() => navigate('/login')}
          className="mb-6 flex items-center gap-2 text-gray-600 hover:text-gray-800"
        >
          <ArrowLeft className="h-4 w-4" />
          
        </Button>
        
        <Card className="shadow-xl">
          <CardHeader className="text-center">
            <CardTitle className="text-2xl font-bold"></CardTitle>
            <CardDescription>
              
            </CardDescription>
          </CardHeader>
          <CardContent>
            <Tabs value={activeTab} onValueChange={setActiveTab} className="w-full">
              <TabsList className="grid w-full grid-cols-2">
                <TabsTrigger value="phone"></TabsTrigger>
                <TabsTrigger value="email"></TabsTrigger>
              </TabsList>
              
              {/*  */}
              <TabsContent value="phone" className="space-y-4">
                {step === 'input' && (
                  <div className="space-y-4">
                    <div className="space-y-2">
                      <Label htmlFor="phone"></Label>
                      <div className="relative">
                        <Phone className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                        <Input
                          id="phone"
                          type="tel"
                          placeholder=""
                          value={phoneForm.phone}
                          onChange={(e) => handlePhoneFormChange('phone', e.target.value)}
                          className="pl-10"
                        />
                      </div>
                    </div>
                    
                    <Button
                      onClick={() => sendCode('phone')}
                      disabled={countdown > 0}
                      className="w-full"
                    >
                      {countdown > 0 ? `${countdown}` : ''}
                    </Button>
                  </div>
                )}
                
                {step === 'verify' && (
                  <div className="space-y-4">
                    <div className="space-y-2">
                      <Label htmlFor="phone-code"></Label>
                      <div className="flex gap-2">
                        <div className="relative flex-1">
                          <Input
                            id="phone-code"
                            placeholder=""
                            value={phoneForm.code}
                            onChange={(e) => handlePhoneFormChange('code', e.target.value)}
                          />
                        </div>
                        <Button
                          type="button"
                          variant="outline"
                          onClick={() => sendCode('phone')}
                          disabled={countdown > 0}
                          className="w-24"
                        >
                          {countdown > 0 ? `${countdown}s` : ''}
                        </Button>
                      </div>
                    </div>
                    
                    <Button
                      onClick={() => verifyCode('phone')}
                      className="w-full"
                    >
                      
                    </Button>
                  </div>
                )}
                
                {step === 'reset' && (
                  <div className="space-y-4">
                    <div className="space-y-2">
                      <Label htmlFor="phone-new-password"></Label>
                      <div className="relative">
                        <Lock className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                        <Input
                          id="phone-new-password"
                          type="password"
                          placeholder=""
                          value={phoneForm.newPassword}
                          onChange={(e) => handlePhoneFormChange('newPassword', e.target.value)}
                          className="pl-10"
                        />
                      </div>
                    </div>
                    
                    <div className="space-y-2">
                      <Label htmlFor="phone-confirm-password"></Label>
                      <div className="relative">
                        <Lock className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                        <Input
                          id="phone-confirm-password"
                          type="password"
                          placeholder=""
                          value={phoneForm.confirmPassword}
                          onChange={(e) => handlePhoneFormChange('confirmPassword', e.target.value)}
                          className="pl-10"
                        />
                      </div>
                    </div>
                    
                    <Button
                      onClick={() => resetPassword('phone')}
                      disabled={isLoading}
                      className="w-full"
                    >
                      {isLoading ? "..." : ""}
                    </Button>
                  </div>
                )}
              </TabsContent>
              
              {/*  */}
              <TabsContent value="email" className="space-y-4">
                {step === 'input' && (
                  <div className="space-y-4">
                    <div className="space-y-2">
                      <Label htmlFor="email"></Label>
                      <div className="relative">
                        <Mail className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                        <Input
                          id="email"
                          type="email"
                          placeholder=""
                          value={emailForm.email}
                          onChange={(e) => handleEmailFormChange('email', e.target.value)}
                          className="pl-10"
                        />
                      </div>
                    </div>
                    
                    <Button
                      onClick={() => sendCode('email')}
                      disabled={countdown > 0}
                      className="w-full"
                    >
                      {countdown > 0 ? `${countdown}` : ''}
                    </Button>
                  </div>
                )}
                
                {step === 'verify' && (
                  <div className="space-y-4">
                    <div className="space-y-2">
                      <Label htmlFor="email-code"></Label>
                      <div className="flex gap-2">
                        <div className="relative flex-1">
                          <Input
                            id="email-code"
                            placeholder=""
                            value={emailForm.code}
                            onChange={(e) => handleEmailFormChange('code', e.target.value)}
                          />
                        </div>
                        <Button
                          type="button"
                          variant="outline"
                          onClick={() => sendCode('email')}
                          disabled={countdown > 0}
                          className="w-24"
                        >
                          {countdown > 0 ? `${countdown}s` : ''}
                        </Button>
                      </div>
                    </div>
                    
                    <Button
                      onClick={() => verifyCode('email')}
                      className="w-full"
                    >
                      
                    </Button>
                  </div>
                )}
                
                {step === 'reset' && (
                  <div className="space-y-4">
                    <div className="space-y-2">
                      <Label htmlFor="email-new-password"></Label>
                      <div className="relative">
                        <Lock className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                        <Input
                          id="email-new-password"
                          type="password"
                          placeholder=""
                          value={emailForm.newPassword}
                          onChange={(e) => handleEmailFormChange('newPassword', e.target.value)}
                          className="pl-10"
                        />
                      </div>
                    </div>
                    
                    <div className="space-y-2">
                      <Label htmlFor="email-confirm-password"></Label>
                      <div className="relative">
                        <Lock className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                        <Input
                          id="email-confirm-password"
                          type="password"
                          placeholder=""
                          value={emailForm.confirmPassword}
                          onChange={(e) => handleEmailFormChange('confirmPassword', e.target.value)}
                          className="pl-10"
                        />
                      </div>
                    </div>
                    
                    <Button
                      onClick={() => resetPassword('email')}
                      disabled={isLoading}
                      className="w-full"
                    >
                      {isLoading ? "..." : ""}
                    </Button>
                  </div>
                )}
              </TabsContent>
            </Tabs>
            
            {/*  */}
            <div className="mt-6 text-center">
              <p className="text-sm text-gray-600">
                
                <Link to="/login" className="text-blue-600 hover:underline ml-1">
                  
                </Link>
              </p>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  );
} 