import React, { useState, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Textarea } from '@/components/ui/textarea';
import { Badge } from '@/components/ui/badge';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog';
import { Copy, Edit, Heart, Search, Filter, MessageCircle, Calendar, Sparkles, Plus } from 'lucide-react';
import { useToast } from '@/hooks/use-toast';

/**
 * 
 */
interface WechatTemplate {
  id: string;
  title: string;
  content: string;
  category: string;
  tags: string[];
  emoji: string;
  wordCount: number;
  isFavorite: boolean;
  useCount: number;
  createdAt: Date;
  occasion?: string;
  mood?: string;
}

/**
 * 
 */
const WechatTemplatePage: React.FC = () => {
  const [templates, setTemplates] = useState<WechatTemplate[]>([]);
  const [filteredTemplates, setFilteredTemplates] = useState<WechatTemplate[]>([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedCategory, setSelectedCategory] = useState<string>('all');
  const [selectedTags, setSelectedTags] = useState<string[]>([]);
  const [selectedOccasion, setSelectedOccasion] = useState<string>('all');
  const [editingTemplate, setEditingTemplate] = useState<WechatTemplate | null>(null);
  const [isAddDialogOpen, setIsAddDialogOpen] = useState(false);
  const { toast } = useToast();

  // 
  const categories = [
    '', '', '', '', '', 
    '', '', '', '', ''
  ];

  // 
  const allTags = [
    '', '', '', '', '', '', '', 
    '', '', '', '', '', '', ''
  ];

  // 
  const occasions = [
    '', '', '', '', '', '', '', 
    '', '', '', '', '', '', ''
  ];

  /**
   * 
   */
  useEffect(() => {
    const sampleTemplates: WechatTemplate[] = [
      {
        id: '1',
        title: '',
        content: '     ',
        category: '',
        tags: ['', '', ''],
        emoji: '',
        wordCount: 45,
        isFavorite: true,
        useCount: 156,
        createdAt: new Date('2024-01-15'),
        occasion: '',
        mood: ''
      },
      {
        id: '2',
        title: '',
        content: '    ',
        category: '',
        tags: ['', '', ''],
        emoji: '',
        wordCount: 38,
        isFavorite: false,
        useCount: 234,
        createdAt: new Date('2024-01-10'),
        occasion: '',
        mood: ''
      },
      {
        id: '3',
        title: '',
        content: '    ',
        category: '',
        tags: ['', '', ''],
        emoji: '',
        wordCount: 42,
        isFavorite: true,
        useCount: 89,
        createdAt: new Date('2024-01-08'),
        occasion: '',
        mood: ''
      },
      {
        id: '4',
        title: '',
        content: '    ',
        category: '',
        tags: ['', '', ''],
        emoji: '',
        wordCount: 39,
        isFavorite: false,
        useCount: 123,
        createdAt: new Date('2024-01-05'),
        occasion: '',
        mood: ''
      },
      {
        id: '5',
        title: '',
        content: '    ',
        category: '',
        tags: ['', '', ''],
        emoji: '',
        wordCount: 41,
        isFavorite: true,
        useCount: 178,
        createdAt: new Date('2024-01-03'),
        occasion: '',
        mood: ''
      },
      {
        id: '6',
        title: '',
        content: '      ',
        category: '',
        tags: ['', '', ''],
        emoji: '',
        wordCount: 36,
        isFavorite: false,
        useCount: 267,
        createdAt: new Date('2024-01-01'),
        occasion: '',
        mood: ''
      },
      {
        id: '7',
        title: '',
        content: '      ',
        category: '',
        tags: ['', '', ''],
        emoji: '',
        wordCount: 44,
        isFavorite: true,
        useCount: 145,
        createdAt: new Date('2023-12-28'),
        occasion: '',
        mood: ''
      },
      {
        id: '8',
        title: '',
        content: '     ',
        category: '',
        tags: ['', '', ''],
        emoji: '',
        wordCount: 37,
        isFavorite: false,
        useCount: 92,
        createdAt: new Date('2023-12-25'),
        occasion: '',
        mood: ''
      }
    ];
    setTemplates(sampleTemplates);
    setFilteredTemplates(sampleTemplates);
  }, []);

  /**
   * 
   */
  useEffect(() => {
    let filtered = templates;

    // 
    if (searchTerm) {
      filtered = filtered.filter(template =>
        template.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
        template.content.toLowerCase().includes(searchTerm.toLowerCase()) ||
        template.tags.some(tag => tag.toLowerCase().includes(searchTerm.toLowerCase()))
      );
    }

    // 
    if (selectedCategory !== 'all') {
      filtered = filtered.filter(template => template.category === selectedCategory);
    }

    // 
    if (selectedTags.length > 0) {
      filtered = filtered.filter(template =>
        selectedTags.some(tag => template.tags.includes(tag))
      );
    }

    // 
    if (selectedOccasion !== 'all') {
      filtered = filtered.filter(template => template.occasion === selectedOccasion);
    }

    setFilteredTemplates(filtered);
  }, [templates, searchTerm, selectedCategory, selectedTags, selectedOccasion]);

  /**
   * 
   */
  const copyTemplate = async (content: string) => {
    try {
      await navigator.clipboard.writeText(content);
      toast({
        title: "",
        description: "",
      });
    } catch (err) {
      toast({
        title: "",
        description: "",
        variant: "destructive",
      });
    }
  };

  /**
   * 
   */
  const toggleFavorite = (id: string) => {
    setTemplates(prev => prev.map(template =>
      template.id === id
        ? { ...template, isFavorite: !template.isFavorite }
        : template
    ));
  };

  /**
   * 
   */
  const incrementUseCount = (id: string) => {
    setTemplates(prev => prev.map(template =>
      template.id === id
        ? { ...template, useCount: template.useCount + 1 }
        : template
    ));
  };

  /**
   * 
   */
  const addTemplate = (template: Omit<WechatTemplate, 'id' | 'createdAt'>) => {
    const newTemplate: WechatTemplate = {
      ...template,
      id: Date.now().toString(),
      createdAt: new Date()
    };
    setTemplates(prev => [newTemplate, ...prev]);
    setIsAddDialogOpen(false);
  };

  /**
   * 
   */
  const updateTemplate = (id: string, updates: Partial<WechatTemplate>) => {
    setTemplates(prev => prev.map(template =>
      template.id === id
        ? { ...template, ...updates }
        : template
    ));
    setEditingTemplate(null);
  };

  /**
   * 
   */
  const deleteTemplate = (id: string) => {
    setTemplates(prev => prev.filter(template => template.id !== id));
  };

  return (
    <div className="container mx-auto p-6 space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold"></h1>
          <p className="text-muted-foreground"></p>
        </div>
        <Dialog open={isAddDialogOpen} onOpenChange={setIsAddDialogOpen}>
          <DialogTrigger asChild>
            <Button>
              <Plus className="w-4 h-4 mr-2" />
              
            </Button>
          </DialogTrigger>
          <DialogContent className="max-w-2xl">
            <DialogHeader>
              <DialogTitle></DialogTitle>
            </DialogHeader>
            <AddTemplateForm onSubmit={addTemplate} categories={categories} allTags={allTags} occasions={occasions} />
          </DialogContent>
        </Dialog>
      </div>

      {/*  */}
      <Card>
        <CardContent className="p-6">
          <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
            <div className="relative">
              <Search className="absolute left-3 top-3 h-4 w-4 text-muted-foreground" />
              <Input
                placeholder="..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="pl-10"
              />
            </div>
            <Select value={selectedCategory} onValueChange={setSelectedCategory}>
              <SelectTrigger>
                <SelectValue placeholder="" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all"></SelectItem>
                {categories.map(category => (
                  <SelectItem key={category} value={category}>{category}</SelectItem>
                ))}
              </SelectContent>
            </Select>
            <Select value={selectedOccasion} onValueChange={setSelectedOccasion}>
              <SelectTrigger>
                <SelectValue placeholder="" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all"></SelectItem>
                {occasions.map(occasion => (
                  <SelectItem key={occasion} value={occasion}>{occasion}</SelectItem>
                ))}
              </SelectContent>
            </Select>
            <Button variant="outline" onClick={() => setSelectedTags([])}>
              <Filter className="w-4 h-4 mr-2" />
              
            </Button>
          </div>
          
          {/*  */}
          <div className="mt-4">
            <div className="flex flex-wrap gap-2">
              {allTags.map(tag => (
                <Badge
                  key={tag}
                  variant={selectedTags.includes(tag) ? "default" : "outline"}
                  className="cursor-pointer"
                  onClick={() => setSelectedTags(prev => 
                    prev.includes(tag) 
                      ? prev.filter(t => t !== tag)
                      : [...prev, tag]
                  )}
                >
                  {tag}
                </Badge>
              ))}
            </div>
          </div>
        </CardContent>
      </Card>

      {/*  */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {filteredTemplates.map(template => (
          <TemplateCard
            key={template.id}
            template={template}
            onCopy={copyTemplate}
            onToggleFavorite={toggleFavorite}
            onIncrementUse={incrementUseCount}
            onEdit={setEditingTemplate}
            onDelete={deleteTemplate}
            categories={categories}
            allTags={allTags}
            occasions={occasions}
          />
        ))}
      </div>

      {/*  */}
      {editingTemplate && (
        <Dialog open={!!editingTemplate} onOpenChange={() => setEditingTemplate(null)}>
          <DialogContent className="max-w-2xl">
            <DialogHeader>
              <DialogTitle></DialogTitle>
            </DialogHeader>
            <EditTemplateForm
              template={editingTemplate}
              onSubmit={(updates) => updateTemplate(editingTemplate.id, updates)}
              categories={categories}
              allTags={allTags}
              occasions={occasions}
            />
          </DialogContent>
        </Dialog>
      )}

      {filteredTemplates.length === 0 && (
        <Card>
          <CardContent className="p-12 text-center">
            <MessageCircle className="w-12 h-12 mx-auto text-muted-foreground mb-4" />
            <h3 className="text-lg font-semibold mb-2"></h3>
            <p className="text-muted-foreground"></p>
          </CardContent>
        </Card>
      )}
    </div>
  );
};

/**
 * 
 */
interface TemplateCardProps {
  template: WechatTemplate;
  onCopy: (content: string) => void;
  onToggleFavorite: (id: string) => void;
  onIncrementUse: (id: string) => void;
  onEdit: (template: WechatTemplate) => void;
  onDelete: (id: string) => void;
  categories: string[];
  allTags: string[];
  occasions: string[];
}

const TemplateCard: React.FC<TemplateCardProps> = ({
  template,
  onCopy,
  onToggleFavorite,
  onIncrementUse,
  onEdit,
  onDelete,
  categories,
  allTags,
  occasions
}) => {
  const handleCopy = () => {
    onCopy(template.content);
    onIncrementUse(template.id);
  };

  return (
    <Card className={`${template.isFavorite ? 'ring-2 ring-yellow-400' : ''} hover:shadow-lg transition-all duration-200`}>
      <CardHeader className="pb-3">
        <div className="flex items-start justify-between">
          <div className="flex-1">
            <CardTitle className="text-lg flex items-center gap-2">
              <span>{template.emoji}</span>
              <span>{template.title}</span>
            </CardTitle>
            <div className="flex items-center gap-2 mt-2">
              <Badge variant="secondary">{template.category}</Badge>
              {template.occasion && <Badge variant="outline">{template.occasion}</Badge>}
              <Badge variant="outline">{template.wordCount}</Badge>
            </div>
          </div>
          <div className="flex items-center gap-1">
            <Button
              variant="ghost"
              size="sm"
              onClick={() => onToggleFavorite(template.id)}
            >
              <Heart className={`w-4 h-4 ${template.isFavorite ? 'fill-red-500 text-red-500' : ''}`} />
            </Button>
            <Button
              variant="ghost"
              size="sm"
              onClick={() => onEdit(template)}
            >
              <Edit className="w-4 h-4" />
            </Button>
          </div>
        </div>
      </CardHeader>
      <CardContent className="space-y-4">
        <div className="bg-muted p-4 rounded-lg">
          <p className="text-sm leading-relaxed whitespace-pre-wrap">{template.content}</p>
        </div>
        
        <div className="flex flex-wrap gap-1">
          {template.tags.map(tag => (
            <Badge key={tag} variant="outline" className="text-xs">
              {tag}
            </Badge>
          ))}
        </div>

        <div className="flex items-center justify-between text-sm text-muted-foreground">
          <span>: {template.useCount}</span>
          <span>: {template.createdAt.toLocaleDateString()}</span>
        </div>

        <div className="flex gap-2">
          <Button
            onClick={handleCopy}
            className="flex-1"
          >
            <Copy className="w-4 h-4 mr-2" />
            
          </Button>
        </div>
      </CardContent>
    </Card>
  );
};

/**
 * 
 */
interface AddTemplateFormProps {
  onSubmit: (template: Omit<WechatTemplate, 'id' | 'createdAt'>) => void;
  categories: string[];
  allTags: string[];
  occasions: string[];
}

const AddTemplateForm: React.FC<AddTemplateFormProps> = ({ onSubmit, categories, allTags, occasions }) => {
  const [formData, setFormData] = useState({
    title: '',
    content: '',
    category: '',
    tags: [] as string[],
    emoji: '',
    wordCount: 0,
    isFavorite: false,
    useCount: 0,
    occasion: '',
    mood: ''
  });

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (formData.title.trim() && formData.content.trim()) {
      onSubmit(formData);
      setFormData({
        title: '',
        content: '',
        category: '',
        tags: [],
        emoji: '',
        wordCount: 0,
        isFavorite: false,
        useCount: 0,
        occasion: '',
        mood: ''
      });
    }
  };

  const updateWordCount = (content: string) => {
    setFormData(prev => ({ ...prev, content, wordCount: content.length }));
  };

  const popularEmojis = ['', '', '', '', '', '', '', '', '', '', '', ''];

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      <div className="grid grid-cols-2 gap-4">
        <div>
          <label className="text-sm font-medium"> *</label>
          <Input
            value={formData.title}
            onChange={(e) => setFormData(prev => ({ ...prev, title: e.target.value }))}
            placeholder=""
            required
          />
        </div>
        <div>
          <label className="text-sm font-medium"></label>
          <Select value={formData.category} onValueChange={(value) => setFormData(prev => ({ ...prev, category: value }))}>
            <SelectTrigger>
              <SelectValue placeholder="" />
            </SelectTrigger>
            <SelectContent>
              {categories.map(category => (
                <SelectItem key={category} value={category}>{category}</SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>
      </div>

      <div>
        <label className="text-sm font-medium"> *</label>
        <Textarea
          value={formData.content}
          onChange={(e) => updateWordCount(e.target.value)}
          placeholder="..."
          rows={4}
          required
        />
        <div className="text-xs text-muted-foreground mt-1">
          : {formData.wordCount} (30-50)
        </div>
      </div>

      <div>
        <label className="text-sm font-medium">Emoji</label>
        <Input
          value={formData.emoji}
          onChange={(e) => setFormData(prev => ({ ...prev, emoji: e.target.value }))}
          placeholder="Emoji"
        />
        <div className="flex flex-wrap gap-2 mt-2">
          {popularEmojis.map(e => (
            <button
              key={e}
              type="button"
              className="text-xl hover:scale-110 transition-transform"
              onClick={() => setFormData(prev => ({ ...prev, emoji: prev.emoji + e }))}
            >
              {e}
            </button>
          ))}
        </div>
      </div>

      <div className="grid grid-cols-2 gap-4">
        <div>
          <label className="text-sm font-medium"></label>
          <Select value={formData.occasion} onValueChange={(value) => setFormData(prev => ({ ...prev, occasion: value }))}>
            <SelectTrigger>
              <SelectValue placeholder="" />
            </SelectTrigger>
            <SelectContent>
              {occasions.map(occasion => (
                <SelectItem key={occasion} value={occasion}>{occasion}</SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>
        <div>
          <label className="text-sm font-medium"></label>
          <Input
            value={formData.mood}
            onChange={(e) => setFormData(prev => ({ ...prev, mood: e.target.value }))}
            placeholder=""
          />
        </div>
      </div>

      <div>
        <label className="text-sm font-medium"></label>
        <div className="flex flex-wrap gap-1 mt-1">
          {allTags.map(tag => (
            <Badge
              key={tag}
              variant={formData.tags.includes(tag) ? "default" : "outline"}
              className="cursor-pointer text-xs"
              onClick={() => setFormData(prev => ({
                ...prev,
                tags: prev.tags.includes(tag)
                  ? prev.tags.filter(t => t !== tag)
                  : [...prev.tags, tag]
              }))}
            >
              {tag}
            </Badge>
          ))}
        </div>
      </div>

      <div className="flex items-center gap-4">
        <div className="flex items-center gap-2">
          <input
            type="checkbox"
            checked={formData.isFavorite}
            onChange={(e) => setFormData(prev => ({ ...prev, isFavorite: e.target.checked }))}
            className="rounded"
          />
          <span className="text-sm"></span>
        </div>
      </div>

      <div className="flex justify-end gap-2">
        <Button type="submit"></Button>
      </div>
    </form>
  );
};

/**
 * 
 */
interface EditTemplateFormProps {
  template: WechatTemplate;
  onSubmit: (updates: Partial<WechatTemplate>) => void;
  categories: string[];
  allTags: string[];
  occasions: string[];
}

const EditTemplateForm: React.FC<EditTemplateFormProps> = ({ template, onSubmit, categories, allTags, occasions }) => {
  const [formData, setFormData] = useState({
    title: template.title,
    content: template.content,
    category: template.category,
    tags: template.tags,
    emoji: template.emoji,
    wordCount: template.wordCount,
    isFavorite: template.isFavorite,
    useCount: template.useCount,
    occasion: template.occasion || '',
    mood: template.mood || ''
  });

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    onSubmit(formData);
  };

  const updateWordCount = (content: string) => {
    setFormData(prev => ({ ...prev, content, wordCount: content.length }));
  };

  const popularEmojis = ['', '', '', '', '', '', '', '', '', '', '', ''];

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      <div className="grid grid-cols-2 gap-4">
        <div>
          <label className="text-sm font-medium"> *</label>
          <Input
            value={formData.title}
            onChange={(e) => setFormData(prev => ({ ...prev, title: e.target.value }))}
            placeholder=""
            required
          />
        </div>
        <div>
          <label className="text-sm font-medium"></label>
          <Select value={formData.category} onValueChange={(value) => setFormData(prev => ({ ...prev, category: value }))}>
            <SelectTrigger>
              <SelectValue placeholder="" />
            </SelectTrigger>
            <SelectContent>
              {categories.map(category => (
                <SelectItem key={category} value={category}>{category}</SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>
      </div>

      <div>
        <label className="text-sm font-medium"> *</label>
        <Textarea
          value={formData.content}
          onChange={(e) => updateWordCount(e.target.value)}
          placeholder="..."
          rows={4}
          required
        />
        <div className="text-xs text-muted-foreground mt-1">
          : {formData.wordCount} (30-50)
        </div>
      </div>

      <div>
        <label className="text-sm font-medium">Emoji</label>
        <Input
          value={formData.emoji}
          onChange={(e) => setFormData(prev => ({ ...prev, emoji: e.target.value }))}
          placeholder="Emoji"
        />
        <div className="flex flex-wrap gap-2 mt-2">
          {popularEmojis.map(e => (
            <button
              key={e}
              type="button"
              className="text-xl hover:scale-110 transition-transform"
              onClick={() => setFormData(prev => ({ ...prev, emoji: prev.emoji + e }))}
            >
              {e}
            </button>
          ))}
        </div>
      </div>

      <div className="grid grid-cols-2 gap-4">
        <div>
          <label className="text-sm font-medium"></label>
          <Select value={formData.occasion} onValueChange={(value) => setFormData(prev => ({ ...prev, occasion: value }))}>
            <SelectTrigger>
              <SelectValue placeholder="" />
            </SelectTrigger>
            <SelectContent>
              {occasions.map(occasion => (
                <SelectItem key={occasion} value={occasion}>{occasion}</SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>
        <div>
          <label className="text-sm font-medium"></label>
          <Input
            value={formData.mood}
            onChange={(e) => setFormData(prev => ({ ...prev, mood: e.target.value }))}
            placeholder=""
          />
        </div>
      </div>

      <div>
        <label className="text-sm font-medium"></label>
        <div className="flex flex-wrap gap-1 mt-1">
          {allTags.map(tag => (
            <Badge
              key={tag}
              variant={formData.tags.includes(tag) ? "default" : "outline"}
              className="cursor-pointer text-xs"
              onClick={() => setFormData(prev => ({
                ...prev,
                tags: prev.tags.includes(tag)
                  ? prev.tags.filter(t => t !== tag)
                  : [...prev.tags, tag]
              }))}
            >
              {tag}
            </Badge>
          ))}
        </div>
      </div>

      <div className="flex items-center gap-4">
        <div className="flex items-center gap-2">
          <input
            type="checkbox"
            checked={formData.isFavorite}
            onChange={(e) => setFormData(prev => ({ ...prev, isFavorite: e.target.checked }))}
            className="rounded"
          />
          <span className="text-sm"></span>
        </div>
      </div>

      <div className="flex justify-end gap-2">
        <Button type="submit"></Button>
      </div>
    </form>
  );
};

export default WechatTemplatePage; 