/**
 * AI API
 */
export interface AIApiResponse {
  success: boolean;
  data?: any;
  error?: string;
  detail?: string;
  message?: string;
  content?: string;
  source?: string;
  platform?: string;
  title?: string;
  hashtags?: string[];
  suggestions?: string[];
}

/**
 * 
 */
export interface PlatformSettings {
  /**  */
  name: string;
  /**  */
  description: string;
  /**  */
  style: string;
  /**  */
  maxLength?: number;
  /**  */
  hashtagStyle?: string;
  /**  */
  specialRequirements?: string[];
}

/**
 * 
 */
export interface GlobalSettings {
  /**  */
  preserveOriginalMeaning: boolean;
  /**  */
  addHashtags: boolean;
  /**  */
  optimizeTitle: boolean;
  /**  */
  generateMultipleVersions: boolean;
  /**  */
  versionCount: number;
}

/**
 * 
 */
export interface ContentAdaptRequest {
  /**  */
  originalContent: string;
  /**  */
  targetPlatforms: string[];
  /**  */
  platformSettings: Record<string, PlatformSettings>;
  /**  */
  globalSettings: GlobalSettings;
  /**  */
  userPreferences?: {
    tone?: string;
    style?: string;
    keywords?: string[];
  };
}

/**
 * 
 */
export interface ContentAdaptResponse {
  /**  */
  success: boolean;
  /**  */
  results?: {
    platform: string;
    content: string;
    title?: string;
    hashtags?: string[];
    suggestions?: string[];
  }[];
  /**  */
  error?: string;
  /**  */
  detail?: string;
}

