/**
 * 
 * 
 */

import React, { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { useToast } from '@/hooks/use-toast';
import { useAuth } from '@/contexts/AuthContext';
import { useNavigate, Link } from 'react-router-dom';
import { ArrowLeft, Eye, EyeOff, Phone, Mail, Lock, User, Send } from 'lucide-react';
import { Checkbox } from '@/components/ui/checkbox';

/**
 * 
 * @returns React 
 */
export default function RegisterPage() {
  const [activeTab, setActiveTab] = useState('phone');
  const [showPassword, setShowPassword] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [countdown, setCountdown] = useState(0);
  const [agreeTerms, setAgreeTerms] = useState(false);
  
  // 
  const [phoneForm, setPhoneForm] = useState({
    phone: '',
    code: '',
    password: '',
    confirmPassword: '',
    nickname: ''
  });
  
  // 
  const [emailForm, setEmailForm] = useState({
    email: '',
    code: '',
    password: '',
    confirmPassword: '',
    nickname: ''
  });
  
  const { toast } = useToast();
  const { register } = useAuth();
  const navigate = useNavigate();

  /**
   * 
   */
  const handlePhoneFormChange = (field: string, value: string) => {
    setPhoneForm(prev => ({ ...prev, [field]: value }));
  };

  /**
   * 
   */
  const handleEmailFormChange = (field: string, value: string) => {
    setEmailForm(prev => ({ ...prev, [field]: value }));
  };

  /**
   * 
   */
  const sendCode = async (type: 'phone' | 'email') => {
    if (countdown > 0) return;
    
    const value = type === 'phone' ? phoneForm.phone : emailForm.email;
    
    if (!value) {
      toast({
        title: "" + (type === 'phone' ? '' : ''),
        variant: "destructive"
      });
      return;
    }
    
    if (type === 'phone' && !/^1[3-9]\d{9}$/.test(value)) {
      toast({
        title: "",
        variant: "destructive"
      });
      return;
    }
    
    if (type === 'email' && !/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(value)) {
      toast({
        title: "",
        variant: "destructive"
      });
      return;
    }
    
    try {
      // API
      toast({
        title: "",
        description: `${type === 'phone' ? '' : ''}`
      });
      
      // 
      setCountdown(60);
      const timer = setInterval(() => {
        setCountdown(prev => {
          if (prev <= 1) {
            clearInterval(timer);
            return 0;
          }
          return prev - 1;
        });
      }, 1000);
    } catch (error) {
      toast({
        title: "",
        description: "",
        variant: "destructive"
      });
    }
  };

  /**
   * 
   */
  const handlePhoneRegister = async () => {
    if (!phoneForm.phone || !phoneForm.code || !phoneForm.password || !phoneForm.confirmPassword) {
      toast({
        title: "",
        variant: "destructive"
      });
      return;
    }
    
    if (phoneForm.password !== phoneForm.confirmPassword) {
      toast({
        title: "",
        variant: "destructive"
      });
      return;
    }
    
    if (!agreeTerms) {
      toast({
        title: "",
        variant: "destructive"
      });
      return;
    }
    
    setIsLoading(true);
    try {
      await register(phoneForm.phone, phoneForm.password, phoneForm.nickname);
      
      toast({
        title: "",
        description: ""
      });
      
      navigate('/');
    } catch (error: any) {
      toast({
        title: "",
        description: error.message || "",
        variant: "destructive"
      });
    } finally {
      setIsLoading(false);
    }
  };

  /**
   * 
   */
  const handleEmailRegister = async () => {
    if (!emailForm.email || !emailForm.code || !emailForm.password || !emailForm.confirmPassword) {
      toast({
        title: "",
        variant: "destructive"
      });
      return;
    }
    
    if (emailForm.password !== emailForm.confirmPassword) {
      toast({
        title: "",
        variant: "destructive"
      });
      return;
    }
    
    if (!agreeTerms) {
      toast({
        title: "",
        variant: "destructive"
      });
      return;
    }
    
    setIsLoading(true);
    try {
      await register(emailForm.email, emailForm.password, emailForm.nickname);
      
      toast({
        title: "",
        description: ""
      });
      
      navigate('/');
    } catch (error: any) {
      toast({
        title: "",
        description: error.message || "",
        variant: "destructive"
      });
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-purple-50 flex items-center justify-center p-4">
      <div className="w-full max-w-md">
        {/*  */}
        <Button
          variant="ghost"
          onClick={() => navigate('/')}
          className="mb-6 flex items-center gap-2 text-gray-600 hover:text-gray-800"
        >
          <ArrowLeft className="h-4 w-4" />
          
        </Button>
        
        <Card className="shadow-xl">
          <CardHeader className="text-center">
            <CardTitle className="text-2xl font-bold"></CardTitle>
            <CardDescription>
              
            </CardDescription>
          </CardHeader>
          <CardContent>
            <Tabs value={activeTab} onValueChange={setActiveTab} className="w-full">
              <TabsList className="grid w-full grid-cols-2">
                <TabsTrigger value="phone"></TabsTrigger>
                <TabsTrigger value="email"></TabsTrigger>
              </TabsList>
              
              {/*  */}
              <TabsContent value="phone" className="space-y-4">
                <div className="space-y-2">
                  <Label htmlFor="phone" className="flex items-center">
                     <span className="text-red-500 ml-1">*</span>
                  </Label>
                  <div className="relative">
                    <Phone className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                    <Input
                      id="phone"
                      type="tel"
                      placeholder=""
                      value={phoneForm.phone}
                      onChange={(e) => handlePhoneFormChange('phone', e.target.value)}
                      className="pl-10"
                    />
                  </div>
                </div>
                
                <div className="space-y-2">
                  <Label htmlFor="phone-code"></Label>
                  <div className="flex gap-2">
                    <div className="relative flex-1">
                      <Input
                        id="phone-code"
                        placeholder=""
                        value={phoneForm.code}
                        onChange={(e) => handlePhoneFormChange('code', e.target.value)}
                      />
                    </div>
                    <Button
                      type="button"
                      variant="outline"
                      onClick={() => sendCode('phone')}
                      disabled={countdown > 0}
                      className="w-24"
                    >
                      {countdown > 0 ? `${countdown}s` : ''}
                    </Button>
                  </div>
                </div>
                
                <div className="space-y-2">
                  <Label htmlFor="phone-nickname"></Label>
                  <div className="relative">
                    <User className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                    <Input
                      id="phone-nickname"
                      placeholder=""
                      value={phoneForm.nickname}
                      onChange={(e) => handlePhoneFormChange('nickname', e.target.value)}
                      className="pl-10"
                    />
                  </div>
                </div>
                
                <div className="space-y-2">
                  <Label htmlFor="phone-password"></Label>
                  <div className="relative">
                    <Lock className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                    <Input
                      id="phone-password"
                      type={showPassword ? "text" : "password"}
                      placeholder=""
                      value={phoneForm.password}
                      onChange={(e) => handlePhoneFormChange('password', e.target.value)}
                      className="pl-10 pr-10"
                    />
                    <Button
                      type="button"
                      variant="ghost"
                      size="sm"
                      className="absolute right-0 top-0 h-full px-3 py-2 hover:bg-transparent"
                      onClick={() => setShowPassword(!showPassword)}
                    >
                      {showPassword ? <EyeOff className="h-4 w-4" /> : <Eye className="h-4 w-4" />}
                    </Button>
                  </div>
                </div>
                
                <div className="space-y-2">
                  <Label htmlFor="phone-confirm-password"></Label>
                  <div className="relative">
                    <Lock className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                    <Input
                      id="phone-confirm-password"
                      type={showConfirmPassword ? "text" : "password"}
                      placeholder=""
                      value={phoneForm.confirmPassword}
                      onChange={(e) => handlePhoneFormChange('confirmPassword', e.target.value)}
                      className="pl-10 pr-10"
                    />
                    <Button
                      type="button"
                      variant="ghost"
                      size="sm"
                      className="absolute right-0 top-0 h-full px-3 py-2 hover:bg-transparent"
                      onClick={() => setShowConfirmPassword(!showConfirmPassword)}
                    >
                      {showConfirmPassword ? <EyeOff className="h-4 w-4" /> : <Eye className="h-4 w-4" />}
                    </Button>
                  </div>
                </div>
                
                <div className="flex items-center space-x-2">
                  <Checkbox
                    id="phone-terms"
                    checked={agreeTerms}
                    onCheckedChange={(checked) => setAgreeTerms(checked as boolean)}
                  />
                  <Label htmlFor="phone-terms" className="text-sm">
                    
                    <Link to="/terms" className="text-blue-600 hover:underline ml-1">
                      
                    </Link>
                    
                    <Link to="/privacy" className="text-blue-600 hover:underline ml-1">
                      
                    </Link>
                  </Label>
                </div>
                
                <Button
                  onClick={handlePhoneRegister}
                  disabled={isLoading}
                  className="w-full"
                >
                  {isLoading ? "..." : ""}
                </Button>
              </TabsContent>
              
              {/*  */}
              <TabsContent value="email" className="space-y-4">
                <div className="space-y-2">
                  <Label htmlFor="email"></Label>
                  <div className="relative">
                    <Mail className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                    <Input
                      id="email"
                      type="email"
                      placeholder=""
                      value={emailForm.email}
                      onChange={(e) => handleEmailFormChange('email', e.target.value)}
                      className="pl-10"
                    />
                  </div>
                </div>
                
                <div className="space-y-2">
                  <Label htmlFor="email-code"></Label>
                  <div className="flex gap-2">
                    <div className="relative flex-1">
                      <Input
                        id="email-code"
                        placeholder=""
                        value={emailForm.code}
                        onChange={(e) => handleEmailFormChange('code', e.target.value)}
                      />
                    </div>
                    <Button
                      type="button"
                      variant="outline"
                      onClick={() => sendCode('email')}
                      disabled={countdown > 0}
                      className="w-24"
                    >
                      {countdown > 0 ? `${countdown}s` : ''}
                    </Button>
                  </div>
                </div>
                
                <div className="space-y-2">
                  <Label htmlFor="email-nickname"></Label>
                  <div className="relative">
                    <User className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                    <Input
                      id="email-nickname"
                      placeholder=""
                      value={emailForm.nickname}
                      onChange={(e) => handleEmailFormChange('nickname', e.target.value)}
                      className="pl-10"
                    />
                  </div>
                </div>
                
                <div className="space-y-2">
                  <Label htmlFor="email-password"></Label>
                  <div className="relative">
                    <Lock className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                    <Input
                      id="email-password"
                      type={showPassword ? "text" : "password"}
                      placeholder=""
                      value={emailForm.password}
                      onChange={(e) => handleEmailFormChange('password', e.target.value)}
                      className="pl-10 pr-10"
                    />
                    <Button
                      type="button"
                      variant="ghost"
                      size="sm"
                      className="absolute right-0 top-0 h-full px-3 py-2 hover:bg-transparent"
                      onClick={() => setShowPassword(!showPassword)}
                    >
                      {showPassword ? <EyeOff className="h-4 w-4" /> : <Eye className="h-4 w-4" />}
                    </Button>
                  </div>
                </div>
                
                <div className="space-y-2">
                  <Label htmlFor="email-confirm-password"></Label>
                  <div className="relative">
                    <Lock className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                    <Input
                      id="email-confirm-password"
                      type={showConfirmPassword ? "text" : "password"}
                      placeholder=""
                      value={emailForm.confirmPassword}
                      onChange={(e) => handleEmailFormChange('confirmPassword', e.target.value)}
                      className="pl-10 pr-10"
                    />
                    <Button
                      type="button"
                      variant="ghost"
                      size="sm"
                      className="absolute right-0 top-0 h-full px-3 py-2 hover:bg-transparent"
                      onClick={() => setShowConfirmPassword(!showConfirmPassword)}
                    >
                      {showConfirmPassword ? <EyeOff className="h-4 w-4" /> : <Eye className="h-4 w-4" />}
                    </Button>
                  </div>
                </div>
                
                <div className="flex items-center space-x-2">
                  <Checkbox
                    id="email-terms"
                    checked={agreeTerms}
                    onCheckedChange={(checked) => setAgreeTerms(checked as boolean)}
                  />
                  <Label htmlFor="email-terms" className="text-sm">
                    
                    <Link to="/terms" className="text-blue-600 hover:underline ml-1">
                      
                    </Link>
                    
                    <Link to="/privacy" className="text-blue-600 hover:underline ml-1">
                      
                    </Link>
                  </Label>
                </div>
                
                <Button
                  onClick={handleEmailRegister}
                  disabled={isLoading}
                  className="w-full"
                >
                  {isLoading ? "..." : ""}
                </Button>
              </TabsContent>
            </Tabs>
            
            {/*  */}
            <div className="mt-6 text-center">
              <p className="text-sm text-gray-600">
                
                <Link to="/login" className="text-blue-600 hover:underline ml-1">
                  
                </Link>
              </p>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}