/**
 * 
 * 
 */

import React, { createContext, useContext, useEffect, useState, ReactNode, useCallback } from 'react';
import { AuthenticationClient } from 'authing-js-sdk';
import { getAuthingConfig } from '@/config/authing';
import { User } from '@/types/user';

/**
 * 
 */
interface AuthState {
  /**  */
  user: User | null;
  /**  */
  isAuthenticated: boolean;
  /**  */
  isLoading: boolean;
  /**  */
  error: string | null;
}

/**
 * 
 */
interface AuthContextType extends AuthState {
  /**  */
  login: (email: string, password: string) => Promise<void>;
  /**  */
  register: (email: string, password: string, nickname: string) => Promise<void>;
  /**  */
  logout: () => Promise<void>;
  /**  */
  setUser: (user: User | null) => void;
  /**  */
  showLogin: () => Promise<void>;
  /**  */
  hideLogin: () => void;
  /**  */
  checkAuth: () => Promise<void>;
}

/**
 * 
 */
const AuthContext = createContext<AuthContextType | undefined>(undefined);

/**
 * 
 */
interface AuthProviderProps {
  /**  */
  children: ReactNode;
}

/**
 * 
 * @param props 
 * @returns React 
 */
export const AuthProvider: React.FC<AuthProviderProps> = ({ children }) => {
  const [authing, setAuthing] = useState<AuthenticationClient | null>(null);
  const [isInitialized, setIsInitialized] = useState(false);
  const [state, setState] = useState<AuthState>({
    user: null,
    isAuthenticated: false,
    isLoading: true,
    error: null,
  });

  /**
   * Authing
   */
  useEffect(() => {
    const initAuthing = async () => {
      try {
        const config = getAuthingConfig();
        const authingClient = new AuthenticationClient({
          appId: config.appId,
          appHost: config.host,
          onError: (code, message, data) => {
            setState(prev => ({
              ...prev,
              error: `Authing error: ${message}`,
            }));
          },
        });

        setAuthing(authingClient);
        setIsInitialized(true);
      } catch (error) {
        setState(prev => ({
          ...prev,
          error: '',
          isLoading: false,
        }));
      }
    };

    initAuthing();
  }, []);

  /**
   * 
   */
  const checkAuth = useCallback(async () => {
    if (!authing) {
      setState(prev => ({
        ...prev,
        isAuthenticated: false,
        isLoading: false,
      }));
      return;
    }

    try {
      // 
      const storedUser = localStorage.getItem('authing_user');
      const storedCode = localStorage.getItem('authing_code');
      const storedState = localStorage.getItem('authing_state');

      if (storedUser && storedCode && storedState) {
        const userInfo = JSON.parse(storedUser);
        const convertedUser: User = {
          id: userInfo.id || userInfo.userId || '',
          username: userInfo.username || userInfo.nickname || '',
          email: userInfo.email || '',
          phone: userInfo.phone || '',
          nickname: userInfo.nickname || userInfo.username || '',
          avatar: userInfo.photo || userInfo.avatar || '',
          createdAt: userInfo.createdAt || new Date().toISOString(),
          updatedAt: userInfo.updatedAt || new Date().toISOString(),
        };

        setState({
          user: convertedUser,
          isAuthenticated: true,
          isLoading: false,
          error: null,
        });
        return;
      }

      // Authing
      const currentUser = await authing.getCurrentUser();
      
      if (currentUser) {
        const convertedUser: User = {
          id: currentUser.id || '',
          username: currentUser.username || currentUser.nickname || '',
          email: currentUser.email || '',
          phone: currentUser.phone || '',
          nickname: currentUser.nickname || currentUser.username || '',
          avatar: currentUser.photo || '',
          createdAt: currentUser.createdAt || new Date().toISOString(),
          updatedAt: currentUser.updatedAt || new Date().toISOString(),
        };

        setState({
          user: convertedUser,
          isAuthenticated: true,
          isLoading: false,
          error: null,
        });
      } else {
        setState({
          user: null,
          isAuthenticated: false,
          isLoading: false,
          error: null,
        });
      }
    } catch (error) {
      setState(prev => ({
        ...prev,
        error: '',
        isLoading: false,
      }));
    }
  }, [authing]);

  /**
   * 
   */
  const login = useCallback(async (email: string, password: string) => {
    if (!authing) throw new Error('');

    try {
      const result = await authing.loginByEmail(email, password);
      if (result) {
        await checkAuth();
      } else {
        throw new Error('');
      }
    } catch (error) {
      setState(prev => ({
        ...prev,
        error: error instanceof Error ? error.message : '',
      }));
      throw error;
    }
  }, [authing, checkAuth]);

  /**
   * 
   */
  const register = useCallback(async (email: string, password: string, nickname: string) => {
    if (!authing) throw new Error('');

    try {
      const result = await authing.registerByEmail(email, password, {
        nickname,
      });
      if (result) {
        await checkAuth();
      } else {
        throw new Error('');
      }
    } catch (error) {
      setState(prev => ({
        ...prev,
        error: error instanceof Error ? error.message : '',
      }));
      throw error;
    }
  }, [authing, checkAuth]);

  /**
   * 
   */
  const logout = useCallback(async () => {
    if (!authing) return;

    try {
      await authing.logout();
      localStorage.removeItem('authing_user');
      localStorage.removeItem('authing_code');
      localStorage.removeItem('authing_state');
      
      setState({
        user: null,
        isAuthenticated: false,
        isLoading: false,
        error: null,
      });
    } catch (error) {
      setState(prev => ({
        ...prev,
        error: '',
      }));
    }
  }, [authing]);

  /**
   * 
   */
  const showLogin = useCallback(async () => {
    if (!isInitialized || !authing) {
      // 
      setTimeout(() => showLogin(), 100);
      return;
    }

    try {
      // Guard
      console.log('');
    } catch (error) {
      setState(prev => ({
        ...prev,
        error: error instanceof Error ? error.message : '',
      }));
    }
  }, [isInitialized, authing]);

  /**
   * 
   */
  const hideLogin = useCallback(() => {
    // AuthenticationClient
  }, []);

  /**
   * 
   */
  const setUser = useCallback((user: User | null) => {
    setState(prev => ({
      ...prev,
      user,
    }));
  }, []);

  /**
   * 
   */
  useEffect(() => {
    if (isInitialized) {
      checkAuth();
    }
  }, [isInitialized, checkAuth]);

  const contextValue: AuthContextType = {
    ...state,
    login,
    register,
    logout,
    setUser,
    showLogin,
    hideLogin,
    checkAuth,
  };

  return (
    <AuthContext.Provider value={contextValue}>
      {children}
    </AuthContext.Provider>
  );
};

/**
 * Hook
 * @returns AuthContextType
 */
export const useAuth = (): AuthContextType => {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
}; 