/**
 * 
 * 
 */

import React, { useState, useEffect } from 'react';
import { Card, CardContent } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Input } from '@/components/ui/input';
import { Textarea } from '@/components/ui/textarea';
import { Label } from '@/components/ui/label';
import { 
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from '@/components/ui/dialog';
import { 
  CheckSquare,
  Square,
  Plus,
  Edit,
  Trash2,
  Clock,
  AlertCircle,
  CheckCircle,
  Circle,
  Search
} from 'lucide-react';
import { useToast } from '@/hooks/use-toast';

/**
 * 
 */
interface TodoItem {
  id: string;
  title: string;
  description?: string;
  completed: boolean;
  priority: 'low' | 'medium' | 'high';
  dueDate?: string;
  category?: string;
  createdAt: string;
  updatedAt: string;
  tags?: string[];
}

/**
 * 
 * @returns React 
 */
export function TodoList() {
  const { toast } = useToast();
  
  // 
  const [todos, setTodos] = useState<TodoItem[]>([]);
  const [filteredTodos, setFilteredTodos] = useState<TodoItem[]>([]);
  const [searchQuery, setSearchQuery] = useState('');
  const [filterStatus, setFilterStatus] = useState<'all' | 'completed' | 'pending'>('all');
  const [filterPriority, setFilterPriority] = useState<'all' | 'low' | 'medium' | 'high'>('all');

  const [isCreateDialogOpen, setIsCreateDialogOpen] = useState(false);
  const [editingTodo, setEditingTodo] = useState<TodoItem | null>(null);
  
  // 
  const [newTodo, setNewTodo] = useState({
    title: '',
    description: '',
    priority: 'medium' as 'low' | 'medium' | 'high',
    dueDate: '',
    category: '',
    tags: ''
  });

  /**
   * 
   */
  useEffect(() => {
    const sampleTodos: TodoItem[] = [
      {
        id: '1',
        title: '',
        description: '',
        completed: false,
        priority: 'high',
        dueDate: '2024-02-15',
        category: '',
        createdAt: '2024-02-10T10:00:00Z',
        updatedAt: '2024-02-10T10:00:00Z',
        tags: ['', '', '']
      },
      {
        id: '2',
        title: '',
        description: '',
        completed: true,
        priority: 'high',
        dueDate: '2024-02-14',
        category: '',
        createdAt: '2024-02-08T14:30:00Z',
        updatedAt: '2024-02-14T16:00:00Z',
        tags: ['', '', '']
      },
      {
        id: '3',
        title: '',
        description: '',
        completed: false,
        priority: 'medium',
        dueDate: '2024-02-20',
        category: '',
        createdAt: '2024-02-12T09:15:00Z',
        updatedAt: '2024-02-12T09:15:00Z',
        tags: ['', '', '']
      },
      {
        id: '4',
        title: '',
        description: '',
        completed: false,
        priority: 'medium',
        dueDate: '2024-02-18',
        category: '',
        createdAt: '2024-02-11T11:20:00Z',
        updatedAt: '2024-02-11T11:20:00Z',
        tags: ['', '', '']
      },
      {
        id: '5',
        title: '',
        description: '',
        completed: false,
        priority: 'low',
        dueDate: '2024-02-16',
        category: '',
        createdAt: '2024-02-13T15:45:00Z',
        updatedAt: '2024-02-13T15:45:00Z',
        tags: ['', '', '']
      }
    ];
    
    setTodos(sampleTodos);
    setFilteredTodos(sampleTodos);
  }, []);

  /**
   * 
   */
  useEffect(() => {
    let filtered = [...todos];

    // 
    if (searchQuery) {
      filtered = filtered.filter(todo => 
        todo.title.toLowerCase().includes(searchQuery.toLowerCase()) ||
        todo.description?.toLowerCase().includes(searchQuery.toLowerCase()) ||
        todo.tags?.some(tag => tag.toLowerCase().includes(searchQuery.toLowerCase()))
      );
    }

    // 
    if (filterStatus === 'completed') {
      filtered = filtered.filter(todo => todo.completed);
    } else if (filterStatus === 'pending') {
      filtered = filtered.filter(todo => !todo.completed);
    }

    // 
    if (filterPriority !== 'all') {
      filtered = filtered.filter(todo => todo.priority === filterPriority);
    }

    // 
    filtered.sort((a, b) => new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime());

    setFilteredTodos(filtered);
  }, [todos, searchQuery, filterStatus, filterPriority]);

  /**
   * 
   */
  const createTodo = () => {
    if (!newTodo.title.trim()) {
      toast({
        title: "",
        description: "",
        variant: "destructive"
      });
      return;
    }

    const tags = newTodo.tags.split(',').map(tag => tag.trim()).filter(tag => tag);
    
    const todo: TodoItem = {
      id: Date.now().toString(),
      title: newTodo.title.trim(),
      description: newTodo.description.trim() || undefined,
      completed: false,
      priority: newTodo.priority,
      dueDate: newTodo.dueDate || undefined,
      category: newTodo.category || undefined,
      createdAt: new Date().toISOString(),
      updatedAt: new Date().toISOString(),
      tags: tags.length > 0 ? tags : undefined
    };

    setTodos(prev => [todo, ...prev]);
    setNewTodo({ title: '', description: '', priority: 'medium', dueDate: '', category: '', tags: '' });
    setIsCreateDialogOpen(false);
    
    toast({
      title: "",
      description: "",
    });
  };

  /**
   * 
   */
  const updateTodo = () => {
    if (!editingTodo) return;

    const updatedTodo = {
      ...editingTodo,
      updatedAt: new Date().toISOString()
    };

    setTodos(prev => prev.map(todo => 
      todo.id === editingTodo.id ? updatedTodo : todo
    ));
    
    setEditingTodo(null);
    
    toast({
      title: "",
      description: "",
    });
  };

  /**
   * 
   */
  const deleteTodo = (id: string) => {
    setTodos(prev => prev.filter(todo => todo.id !== id));
    toast({
      title: "",
      description: "",
    });
  };

  /**
   * 
   */
  const toggleComplete = (id: string) => {
    setTodos(prev => prev.map(todo => 
      todo.id === id ? { ...todo, completed: !todo.completed, updatedAt: new Date().toISOString() } : todo
    ));
  };

  /**
   * 
   */
  const getPriorityColor = (priority: string) => {
    switch (priority) {
      case 'high':
        return 'bg-red-100 text-red-800 border-red-200';
      case 'medium':
        return 'bg-yellow-100 text-yellow-800 border-yellow-200';
      case 'low':
        return 'bg-green-100 text-green-800 border-green-200';
      default:
        return 'bg-gray-100 text-gray-800 border-gray-200';
    }
  };

  /**
   * 
   */
  const getPriorityLabel = (priority: string) => {
    switch (priority) {
      case 'high':
        return '';
      case 'medium':
        return '';
      case 'low':
        return '';
      default:
        return '';
    }
  };

  /**
   * 
   */
  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('zh-CN', {
      month: 'short',
      day: 'numeric'
    });
  };

  /**
   * 
   */
  const isOverdue = (dueDate: string) => {
    return new Date(dueDate) < new Date() && new Date(dueDate).toDateString() !== new Date().toDateString();
  };

  /**
   * 
   */
  const getStats = () => {
    const total = todos.length;
    const completed = todos.filter(todo => todo.completed).length;
    const pending = total - completed;
    const overdue = todos.filter(todo => todo.dueDate && isOverdue(todo.dueDate) && !todo.completed).length;

    return { total, completed, pending, overdue };
  };

  const stats = getStats();

  return (
    <div className="space-y-6">
      {/*  */}
      <div className="grid grid-cols-4 gap-4">
        <Card>
          <CardContent className="p-4">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm text-gray-600"></p>
                <p className="text-2xl font-bold">{stats.total}</p>
              </div>
              <CheckSquare className="w-8 h-8 text-blue-500" />
            </div>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="p-4">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm text-gray-600"></p>
                <p className="text-2xl font-bold text-green-600">{stats.completed}</p>
              </div>
              <CheckCircle className="w-8 h-8 text-green-500" />
            </div>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="p-4">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm text-gray-600"></p>
                <p className="text-2xl font-bold text-orange-600">{stats.pending}</p>
              </div>
              <Circle className="w-8 h-8 text-orange-500" />
            </div>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="p-4">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm text-gray-600"></p>
                <p className="text-2xl font-bold text-red-600">{stats.overdue}</p>
              </div>
              <AlertCircle className="w-8 h-8 text-red-500" />
            </div>
          </CardContent>
        </Card>
      </div>

      {/*  */}
      <Card>
        <CardContent className="p-4">
          <div className="flex flex-wrap gap-4 items-center">
            {/*  */}
            <div className="relative flex-1 min-w-[200px]">
              <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 w-4 h-4" />
              <Input
                placeholder="..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="pl-10"
              />
            </div>

            {/*  */}
            <div className="flex gap-2">
              <Button
                variant={filterStatus === 'all' ? 'default' : 'outline'}
                size="sm"
                onClick={() => setFilterStatus('all')}
              >
                
              </Button>
              <Button
                variant={filterStatus === 'pending' ? 'default' : 'outline'}
                size="sm"
                onClick={() => setFilterStatus('pending')}
              >
                
              </Button>
              <Button
                variant={filterStatus === 'completed' ? 'default' : 'outline'}
                size="sm"
                onClick={() => setFilterStatus('completed')}
              >
                
              </Button>
            </div>

            {/*  */}
            <div className="flex gap-2">
              <Button
                variant={filterPriority === 'all' ? 'default' : 'outline'}
                size="sm"
                onClick={() => setFilterPriority('all')}
              >
                
              </Button>
              <Button
                variant={filterPriority === 'high' ? 'default' : 'outline'}
                size="sm"
                onClick={() => setFilterPriority('high')}
              >
                
              </Button>
              <Button
                variant={filterPriority === 'medium' ? 'default' : 'outline'}
                size="sm"
                onClick={() => setFilterPriority('medium')}
              >
                
              </Button>
              <Button
                variant={filterPriority === 'low' ? 'default' : 'outline'}
                size="sm"
                onClick={() => setFilterPriority('low')}
              >
                
              </Button>
            </div>

            {/*  */}
            <Dialog open={isCreateDialogOpen} onOpenChange={setIsCreateDialogOpen}>
              <DialogTrigger asChild>
                <Button>
                  <Plus className="w-4 h-4 mr-2" />
                  
                </Button>
              </DialogTrigger>
              <DialogContent>
                <DialogHeader>
                  <DialogTitle></DialogTitle>
                  <DialogDescription>
                    
                  </DialogDescription>
                </DialogHeader>
                <div className="space-y-4">
                  <div>
                    <Label> *</Label>
                    <Input
                      value={newTodo.title}
                      onChange={(e) => setNewTodo(prev => ({ ...prev, title: e.target.value }))}
                      placeholder=""
                    />
                  </div>
                  <div>
                    <Label></Label>
                    <Textarea
                      value={newTodo.description}
                      onChange={(e) => setNewTodo(prev => ({ ...prev, description: e.target.value }))}
                      placeholder=""
                      rows={3}
                    />
                  </div>
                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <Label></Label>
                      <select
                        value={newTodo.priority}
                        onChange={(e) => setNewTodo(prev => ({ ...prev, priority: e.target.value as 'low' | 'medium' | 'high' }))}
                        className="w-full p-2 border rounded-md"
                      >
                        <option value="low"></option>
                        <option value="medium"></option>
                        <option value="high"></option>
                      </select>
                    </div>
                    <div>
                      <Label></Label>
                      <Input
                        type="date"
                        value={newTodo.dueDate}
                        onChange={(e) => setNewTodo(prev => ({ ...prev, dueDate: e.target.value }))}
                      />
                    </div>
                  </div>
                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <Label></Label>
                      <Input
                        value={newTodo.category}
                        onChange={(e) => setNewTodo(prev => ({ ...prev, category: e.target.value }))}
                        placeholder=""
                      />
                    </div>
                    <div>
                      <Label></Label>
                      <Input
                        value={newTodo.tags}
                        onChange={(e) => setNewTodo(prev => ({ ...prev, tags: e.target.value }))}
                        placeholder=",,"
                      />
                    </div>
                  </div>
                </div>
                <DialogFooter>
                  <Button variant="outline" onClick={() => setIsCreateDialogOpen(false)}>
                    
                  </Button>
                  <Button onClick={createTodo}>
                    
                  </Button>
                </DialogFooter>
              </DialogContent>
            </Dialog>
          </div>
        </CardContent>
      </Card>

      {/*  */}
      <div className="space-y-2">
        {filteredTodos.map((todo) => (
          <Card key={todo.id} className={`transition-all ${todo.completed ? 'opacity-60' : ''}`}>
            <CardContent className="p-4">
              <div className="flex items-start gap-3">
                <Button
                  variant="ghost"
                  size="sm"
                  onClick={() => toggleComplete(todo.id)}
                  className="mt-0.5"
                >
                  {todo.completed ? (
                    <CheckSquare className="w-5 h-5 text-green-500" />
                  ) : (
                    <Square className="w-5 h-5" />
                  )}
                </Button>
                
                <div className="flex-1">
                  <div className="flex items-start justify-between">
                    <div className="flex-1">
                      <h3 className={`font-medium ${todo.completed ? 'line-through text-gray-500' : ''}`}>
                        {todo.title}
                      </h3>
                      {todo.description && (
                        <p className={`text-sm text-gray-600 mt-1 ${todo.completed ? 'line-through' : ''}`}>
                          {todo.description}
                        </p>
                      )}
                      
                      <div className="flex items-center gap-2 mt-2">
                        <Badge className={getPriorityColor(todo.priority)}>
                          {getPriorityLabel(todo.priority)}
                        </Badge>
                        {todo.category && (
                          <Badge variant="outline" className="text-xs">
                            {todo.category}
                          </Badge>
                        )}
                        {todo.dueDate && (
                          <div className={`flex items-center gap-1 text-xs ${
                            isOverdue(todo.dueDate) ? 'text-red-500' : 'text-gray-500'
                          }`}>
                            <Clock className="w-3 h-3" />
                            {formatDate(todo.dueDate)}
                            {isOverdue(todo.dueDate) && <span></span>}
                          </div>
                        )}
                      </div>
                      
                      {todo.tags && todo.tags.length > 0 && (
                        <div className="flex gap-1 mt-2">
                          {todo.tags.map((tag, index) => (
                            <Badge key={index} variant="outline" className="text-xs">
                              {tag}
                            </Badge>
                          ))}
                        </div>
                      )}
                    </div>
                    
                    <div className="flex gap-1">
                      <Button
                        size="sm"
                        variant="ghost"
                        onClick={() => setEditingTodo(todo)}
                      >
                        <Edit className="w-4 h-4" />
                      </Button>
                      <Button
                        size="sm"
                        variant="ghost"
                        onClick={() => deleteTodo(todo.id)}
                      >
                        <Trash2 className="w-4 h-4" />
                      </Button>
                    </div>
                  </div>
                </div>
              </div>
            </CardContent>
          </Card>
        ))}
      </div>

      {/*  */}
      {filteredTodos.length === 0 && (
        <Card>
          <CardContent className="p-12 text-center">
            <CheckSquare className="w-12 h-12 text-gray-400 mx-auto mb-4" />
            <h3 className="text-lg font-medium text-gray-900 mb-2"></h3>
            <p className="text-gray-600 mb-4">
              {searchQuery || filterStatus !== 'all' || filterPriority !== 'all' 
                ? '' 
                : ''
              }
            </p>
            {!searchQuery && filterStatus === 'all' && filterPriority === 'all' && (
              <Button onClick={() => setIsCreateDialogOpen(true)}>
                <Plus className="w-4 h-4 mr-2" />
                
              </Button>
            )}
          </CardContent>
        </Card>
      )}

      {/*  */}
      {editingTodo && (
        <Dialog open={!!editingTodo} onOpenChange={() => setEditingTodo(null)}>
          <DialogContent>
            <DialogHeader>
              <DialogTitle></DialogTitle>
            </DialogHeader>
            <div className="space-y-4">
              <div>
                <Label></Label>
                <Input
                  value={editingTodo.title}
                  onChange={(e) => setEditingTodo(prev => prev ? { ...prev, title: e.target.value } : null)}
                />
              </div>
              <div>
                <Label></Label>
                <Textarea
                  value={editingTodo.description || ''}
                  onChange={(e) => setEditingTodo(prev => prev ? { ...prev, description: e.target.value } : null)}
                  rows={3}
                />
              </div>
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <Label></Label>
                  <select
                    value={editingTodo.priority}
                    onChange={(e) => setEditingTodo(prev => prev ? { ...prev, priority: e.target.value as 'low' | 'medium' | 'high' } : null)}
                    className="w-full p-2 border rounded-md"
                  >
                    <option value="low"></option>
                    <option value="medium"></option>
                    <option value="high"></option>
                  </select>
                </div>
                <div>
                  <Label></Label>
                  <Input
                    type="date"
                    value={editingTodo.dueDate || ''}
                    onChange={(e) => setEditingTodo(prev => prev ? { ...prev, dueDate: e.target.value } : null)}
                  />
                </div>
              </div>
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <Label></Label>
                  <Input
                    value={editingTodo.category || ''}
                    onChange={(e) => setEditingTodo(prev => prev ? { ...prev, category: e.target.value } : null)}
                  />
                </div>
                <div>
                  <Label></Label>
                  <Input
                    value={editingTodo.tags?.join(', ') || ''}
                    onChange={(e) => setEditingTodo(prev => prev ? { 
                      ...prev, 
                      tags: e.target.value.split(',').map(tag => tag.trim()).filter(tag => tag)
                    } : null)}
                  />
                </div>
              </div>
            </div>
            <DialogFooter>
              <Button variant="outline" onClick={() => setEditingTodo(null)}>
                
              </Button>
              <Button onClick={updateTodo}>
                
              </Button>
            </DialogFooter>
          </DialogContent>
        </Dialog>
      )}
    </div>
  );
} 