/**
 * 
 *  Authing 
 */

import React, { useEffect, useState } from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';
import { useAuth } from '@/contexts/AuthContext';
import { AuthenticationClient } from 'authing-js-sdk';
import { getAuthingConfig } from '@/config/authing';

/**
 * 
 * @returns React 
 */
const Callback: React.FC = () => {
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const { checkAuth } = useAuth();
  const [status, setStatus] = useState<'loading' | 'success' | 'error'>('loading');
  const [message, setMessage] = useState('...');

  useEffect(() => {
    const handleCallback = async () => {
      try {
        setStatus('loading');
        setMessage('...');

        // 
        const error = searchParams.get('error');
        const errorDescription = searchParams.get('error_description');

        if (error) {
          setStatus('error');
          setMessage(`: ${errorDescription || error}`);
          return;
        }

        // 
        const code = searchParams.get('code');
        const state = searchParams.get('state');

        if (!code) {
          setStatus('error');
          setMessage('');
          return;
        }

        //  AuthenticationClient 
        const config = getAuthingConfig();
        const authingClient = new AuthenticationClient({
          appId: config.appId,
          appHost: config.host,
        });

        try {
          console.log(':', code);
          console.log(':', state);
          
          setStatus('success');
          setMessage('...');
          
          // 
          try {
            console.log('...');
            
            // 
            const userInfo = {
              id: code, // ID
              username: '',
              email: '',
              phone: '',
              nickname: '',
              avatar: '',
              authCode: code,
              state: state,
              loginTime: new Date().toISOString()
            };
            
            console.log(':', userInfo);
            
            // 
            localStorage.setItem('authing_user', JSON.stringify(userInfo));
            localStorage.setItem('authing_code', code);
            localStorage.setItem('authing_state', state);
            
            setMessage('...');
            
          } catch (tokenError) {
            console.error(':', tokenError);
            // 
            setMessage('...');
          }
          
          // 
          setTimeout(() => {
            navigate('/', { replace: true });
          }, 1500);

        } catch (tokenError) {
          console.error(':', tokenError);
          setStatus('error');
          setMessage(`: ${tokenError instanceof Error ? tokenError.message : ''}`);
        }

      } catch (error) {
        console.error(':', error);
        setStatus('error');
        setMessage(`: ${error instanceof Error ? error.message : ''}`);
      }
    };

    handleCallback();
  }, [searchParams, checkAuth, navigate]);

  return (
    <div className="min-h-screen flex items-center justify-center bg-gray-50">
      <div className="max-w-md w-full space-y-8">
        <div className="text-center">
          <div className="mx-auto flex items-center justify-center h-12 w-12 rounded-full bg-blue-100">
            {status === 'loading' && (
              <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
            )}
            {status === 'success' && (
              <svg className="h-8 w-8 text-green-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
              </svg>
            )}
            {status === 'error' && (
              <svg className="h-8 w-8 text-red-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
              </svg>
            )}
          </div>
          
          <h2 className="mt-6 text-3xl font-extrabold text-gray-900">
            {status === 'loading' && '...'}
            {status === 'success' && ''}
            {status === 'error' && ''}
          </h2>
          
          <p className="mt-2 text-sm text-gray-600">
            {message}
          </p>
        </div>

        {status === 'error' && (
          <div className="mt-8 space-y-4">
            <div className="bg-red-50 border border-red-200 rounded-lg p-4">
              <div className="flex">
                <div className="flex-shrink-0">
                  <svg className="h-5 w-5 text-red-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-2.5L13.732 4c-.77-.833-1.964-.833-2.732 0L3.732 16.5c-.77.833.192 2.5 1.732 2.5z" />
                  </svg>
                </div>
                <div className="ml-3">
                  <h3 className="text-sm font-medium text-red-800">
                    
                  </h3>
                  <div className="mt-2 text-sm text-red-700">
                    <p></p>
                    <ul className="list-disc list-inside mt-1 space-y-1">
                      <li></li>
                      <li></li>
                      <li></li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
            
            <div className="flex space-x-3">
              <button
                onClick={() => window.location.reload()}
                className="flex-1 bg-blue-600 text-white px-4 py-2 rounded-md hover:bg-blue-700 transition-colors"
              >
                
              </button>
              <button
                onClick={() => navigate('/')}
                className="flex-1 bg-gray-600 text-white px-4 py-2 rounded-md hover:bg-gray-700 transition-colors"
              >
                
              </button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default Callback; 