/**
 *  - 
 * 
 */

import React, { useState, useEffect, useMemo } from 'react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Input } from '@/components/ui/input';
import { Textarea } from '@/components/ui/textarea';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { 
  Sparkles,
  RefreshCw,
  Copy,
  Save,
  Lightbulb,
  Target,
  Users,
  MapPin,
  AlertTriangle,
  Heart,
  Star,
  Zap,
  Plus,
  Trash2,
  Shuffle,
  Download,
  FileText,
  Video,
  Music,
  Camera,
  Clock
} from 'lucide-react';
import { useToast } from '@/hooks/use-toast';

/**
 * 
 */
interface CubeDimension {
  id: string;
  name: string;
  description: string;
  icon: React.ReactNode;
  defaultItems: string[];
}

/**
 * 
 */
interface CreativeResult {
  id: string;
  combination: Record<string, string>;
  generatedContent: string;
  contentType: 'text' | 'video';
  timestamp: string;
  tags: string[];
}

/**
 * 
 */
interface VideoScript {
  sceneNumber: string;
  sceneDescription: string;
  dialogue: string;
  tone: string;
  emotion: string;
  bgm: string;
  soundEffect: string;
  shotType: string;
  duration: number;
}

/**
 * 
 * @returns React 
 */
export function CreativeCube() {
  const { toast } = useToast();
  
  // 
  const dimensions: CubeDimension[] = useMemo(() => [
    {
      id: 'target_audience',
      name: '',
      description: '',
      icon: <Users className="w-4 h-4" />,
      defaultItems: ['', '', '', '', '', '', '', '', '']
    },
    {
      id: 'scenarios',
      name: '',
      description: '',
      icon: <MapPin className="w-4 h-4" />,
      defaultItems: ['', '', '', '', '', '', '', '', '']
    },
    {
      id: 'pain_points',
      name: '',
      description: '',
      icon: <AlertTriangle className="w-4 h-4" />,
      defaultItems: ['', '', '', '', '', '', '', '', '']
    },
    {
      id: 'emotions',
      name: '',
      description: '',
      icon: <Heart className="w-4 h-4" />,
      defaultItems: ['', '', '', '', '', '', '', '', '']
    },
    {
      id: 'benefits',
      name: '',
      description: '',
      icon: <Star className="w-4 h-4" />,
      defaultItems: ['', '', '', '', '', '', '', '', '']
    },
    {
      id: 'channels',
      name: '',
      description: '',
      icon: <Zap className="w-4 h-4" />,
      defaultItems: ['', '', '', '', '', 'KOL', '', '', '']
    },
    {
      id: 'formats',
      name: '',
      description: '',
      icon: <Target className="w-4 h-4" />,
      defaultItems: ['', '', '', '', 'H5', '', '', '', '']
    },
    {
      id: 'tones',
      name: '',
      description: '',
      icon: <Lightbulb className="w-4 h-4" />,
      defaultItems: ['', '', '', '', '', '', '', '', '']
    },
    {
      id: 'trends',
      name: '',
      description: '',
      icon: <Sparkles className="w-4 h-4" />,
      defaultItems: ['AI', '', '', '', '', '', '', '', '']
    }
  ], []);

  // 
  const [cubeData, setCubeData] = useState<Record<string, string[]>>({});
  const [selectedItems, setSelectedItems] = useState<Record<string, string>>({});
  const [generatedIdeas, setGeneratedIdeas] = useState<CreativeResult[]>([]);
  const [isGenerating, setIsGenerating] = useState(false);
  const [currentContent, setCurrentContent] = useState<string>('');
  const [currentContentType, setCurrentContentType] = useState<'text' | 'video'>('text');
  const [videoScript, setVideoScript] = useState<VideoScript[]>([]);

  /**
   * 
   */
  useEffect(() => {
    const initialData: Record<string, string[]> = {};
    dimensions.forEach(dim => {
      initialData[dim.id] = [...dim.defaultItems];
    });
    setCubeData(initialData);
  }, [dimensions]);

  /**
   * 
   */
  const addItemToCube = (dimensionId: string, newItem: string) => {
    if (!newItem.trim()) return;
    
    setCubeData(prev => ({
      ...prev,
      [dimensionId]: [...(prev[dimensionId] || []), newItem.trim()]
    }));
  };

  /**
   *  - 
   */
  const smartRandomGenerate = () => {
    const newSelection: Record<string, string> = { ...selectedItems };
    dimensions.forEach(dim => {
      // 
      if (!selectedItems[dim.id]) {
        const items = cubeData[dim.id] || [];
        if (items.length > 0) {
          const randomIndex = Math.floor(Math.random() * items.length);
          newSelection[dim.id] = items[randomIndex];
        }
      }
    });
    setSelectedItems(newSelection);
  };

  /**
   * AI Prompt
   */
  const buildPrompt = () => {
    const {
      target_audience = '',
      scenarios = '',
      pain_points = '',
      emotions = '',
      benefits = '',
      channels = '',
      formats = '',
      tones = '',
      trends = ''
    } = selectedItems;

    return `${formats}${target_audience}${scenarios}${pain_points}${benefits}${emotions}${tones}${channels}${trends}

`;
  };

  /**
   * 
   */
  const generateTextContent = (prompt: string) => {
    const { target_audience, scenarios, pain_points, tones, benefits, emotions } = selectedItems;
    
    // 
    let contentStyle = '';
    switch (tones) {
      case '':
        contentStyle = '';
        break;
      case '':
        contentStyle = '';
        break;
      case '':
        contentStyle = '';
        break;
      case '':
        contentStyle = '';
        break;
      default:
        contentStyle = '';
    }

    return ` ${target_audience}

${contentStyle}${target_audience}${scenarios}${pain_points}

 ${benefits}
 ${emotions}


${generateTitle()}


${generateBody()}


${generateCallToAction()}

#${target_audience} #${scenarios} #${benefits} #${tones}`;
  };

  /**
   * 
   */
  const generateTitle = () => {
    const { target_audience, pain_points, benefits, tones } = selectedItems;
    
    const titles = {
      '': [
        `${target_audience}${pain_points}`,
        `${target_audience}${pain_points}`,
        `${target_audience}${pain_points}`
      ],
      '': [
        `${target_audience}${pain_points}`,
        `${target_audience}${benefits}`,
        `${target_audience}${pain_points}`
      ],
      '': [
        `${target_audience}${pain_points}`,
        `${target_audience}${benefits}`,
        `${target_audience}${pain_points}`
      ],
      '': [
        `${target_audience}${pain_points}`,
        `${target_audience}${benefits}`,
        `${target_audience}${pain_points}`
      ]
    };

    const titleList = titles[tones as keyof typeof titles] || titles[''];
    return titleList[Math.floor(Math.random() * titleList.length)];
  };

  /**
   * 
   */
  const generateBody = () => {
    const { target_audience, scenarios, pain_points, benefits, emotions, tones } = selectedItems;
    
    const scenarioTexts = [
      `${target_audience}${scenarios}${pain_points}`,
      `${scenarios}${target_audience}${pain_points}`,
      `${target_audience}${scenarios}${pain_points}`
    ];

    const solutions = [
      `${benefits}`,
      `${benefits}`,
      `${benefits}`
    ];

    const emotionTexts = [
      `${pain_points}${emotions}`,
      `${target_audience}${scenarios}${emotions}`,
      `${emotions}${benefits}`
    ];

    return `${scenarioTexts[Math.floor(Math.random() * scenarioTexts.length)]}

${solutions[Math.floor(Math.random() * solutions.length)]}

${emotionTexts[Math.floor(Math.random() * emotionTexts.length)]}

${target_audience}${scenarios}`;
  };

  /**
   * 
   */
  const generateCallToAction = () => {
    const { target_audience, benefits, tones } = selectedItems;
    
    const ctas = {
      '': [
        ` ${target_audience}${benefits}`,
        ` ${target_audience}${benefits}`,
        ` ${target_audience}${benefits}`
      ],
      '': [
        ` ${target_audience}${benefits}`,
        ` ${target_audience}${benefits}`,
        ` ${target_audience}${benefits}`
      ],
      '': [
        ` ${target_audience}${benefits}`,
        ` ${target_audience}${benefits}`,
        ` ${target_audience}${benefits}`
      ],
      '': [
        ` ${target_audience}${benefits}`,
        ` ${target_audience}${benefits}`,
        ` ${target_audience}${benefits}`
      ]
    };

    const ctaList = ctas[tones as keyof typeof ctas] || ctas[''];
    return ctaList[Math.floor(Math.random() * ctaList.length)];
  };

  /**
   * 
   */
  const generateVideoScript = () => {
    const { target_audience, scenarios, pain_points, benefits, emotions, tones } = selectedItems;
    
    const scripts: VideoScript[] = [
      {
        sceneNumber: 'Scene1',
        sceneDescription: `${target_audience}${scenarios}${pain_points}`,
        dialogue: `"...${pain_points}"`,
        tone: tones,
        emotion: '',
        bgm: '',
        soundEffect: '',
        shotType: '',
        duration: 3
      },
      {
        sceneNumber: 'Scene2',
        sceneDescription: '${benefits}',
        dialogue: `"${benefits}"`,
        tone: tones,
        emotion: '',
        bgm: '',
        soundEffect: '',
        shotType: '',
        duration: 4
      },
      {
        sceneNumber: 'Scene3',
        sceneDescription: '${emotions}',
        dialogue: `"${emotions}"`,
        tone: tones,
        emotion: emotions,
        bgm: '',
        soundEffect: '',
        shotType: '',
        duration: 3
      },
      {
        sceneNumber: 'End',
        sceneDescription: '',
        dialogue: `"${target_audience}${benefits}"`,
        tone: tones,
        emotion: '',
        bgm: '',
        soundEffect: '',
        shotType: '',
        duration: 2
      }
    ];

    return scripts;
  };

  /**
   * 
   */
  const generateIdea = async () => {
    if (Object.keys(selectedItems).length < 3) {
      toast({
        title: "3",
        description: "",
        variant: "destructive"
      });
      return;
    }

    setIsGenerating(true);
    try {
      // AI
      await new Promise(resolve => setTimeout(resolve, 2000));
      
      const format = selectedItems.formats || '';
      const isVideo = format.includes('') || format.includes('');
      
      let generatedContent = '';
      let contentType: 'text' | 'video' = 'text';
      
      if (isVideo) {
        contentType = 'video';
        const script = generateVideoScript();
        setVideoScript(script);
        
        generatedContent = ` 

 ${selectedItems.target_audience}  ${selectedItems.scenarios}  ${selectedItems.pain_points}
 ${selectedItems.benefits}
 ${selectedItems.emotions}
 ${selectedItems.tones}

 
${script.map(scene => `
${scene.sceneNumber}${scene.sceneDescription}
${scene.dialogue}
${scene.emotion}
${scene.shotType}
${scene.duration}
BGM${scene.bgm}
${scene.soundEffect}
`).join('')}

 ${selectedItems.tones}BGM
 ${selectedItems.emotions}
 ${selectedItems.channels}`;
      } else {
        contentType = 'text';
        generatedContent = generateTextContent(buildPrompt());
      }

      setCurrentContent(generatedContent);
      setCurrentContentType(contentType);

      // 
      const newResult: CreativeResult = {
        id: Date.now().toString(),
        combination: { ...selectedItems },
        generatedContent,
        contentType,
        timestamp: new Date().toISOString(),
        tags: Object.values(selectedItems).slice(0, 3)
      };

      setGeneratedIdeas(prev => [newResult, ...prev]);

      toast({
        title: "",
        description: `${isVideo ? '' : ''}`,
      });
    } catch {
      toast({
        title: "",
        description: "",
        variant: "destructive"
      });
    } finally {
      setIsGenerating(false);
    }
  };

  /**
   * Excel
   */
  const exportToExcel = () => {
    if (currentContentType !== 'video' || videoScript.length === 0) {
      toast({
        title: "",
        description: "Excel",
        variant: "destructive"
      });
      return;
    }

    // CSV
    const headers = ['', '', '', '', '', '', '', '', 's'];
    const csvContent = [
      headers.join(','),
      ...videoScript.map(scene => [
        scene.sceneNumber,
        scene.sceneDescription,
        scene.dialogue,
        scene.tone,
        scene.emotion,
        scene.bgm,
        scene.soundEffect,
        scene.shotType,
        scene.duration
      ].join(','))
    ].join('\n');

    // 
    const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
    const link = document.createElement('a');
    const url = URL.createObjectURL(blob);
    link.setAttribute('href', url);
    link.setAttribute('download', `_${new Date().toISOString().split('T')[0]}.csv`);
    link.style.visibility = 'hidden';
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);

    toast({
      title: "",
      description: "CSV",
    });
  };

  /**
   * 
   */
  const copyIdea = (content: string) => {
    navigator.clipboard.writeText(content);
    toast({
      title: "",
      description: "",
    });
  };

  /**
   * 
   */
  const saveIdea = () => {
    // 
    toast({
      title: "",
      description: "",
    });
  };

  return (
    <div className="space-y-6">
      {/*  */}
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <Sparkles className="w-5 h-5" />
            
          </CardTitle>
          <CardDescription>
            AI
          </CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-3 gap-4">
            {dimensions.map((dimension) => (
              <Card key={dimension.id} className="border-2 border-dashed border-gray-200">
                <CardHeader className="pb-2">
                  <CardTitle className="text-sm flex items-center gap-2">
                    {dimension.icon}
                    {dimension.name}
                  </CardTitle>
                  <CardDescription className="text-xs">
                    {dimension.description}
                  </CardDescription>
                </CardHeader>
                <CardContent className="space-y-2">
                  {/*  */}
                  {selectedItems[dimension.id] && (
                    <div className="p-2 bg-primary/10 rounded-md">
                      <Badge variant="secondary" className="text-xs">
                        {selectedItems[dimension.id]}
                      </Badge>
                    </div>
                  )}
                  
                  {/*  */}
                  <div className="space-y-1 max-h-32 overflow-y-auto">
                    {cubeData[dimension.id]?.map((item, index) => (
                      <div key={index} className="flex items-center p-1 hover:bg-gray-50 rounded">
                        <span 
                          className="text-xs cursor-pointer hover:text-primary flex-1"
                          onClick={() => setSelectedItems(prev => ({
                            ...prev,
                            [dimension.id]: item
                          }))}
                        >
                          {item}
                        </span>
                      </div>
                    ))}
                  </div>
                  
                  {/*  */}
                  <div className="flex gap-1">
                    <Input
                      placeholder=""
                      className="text-xs h-6"
                      onKeyDown={(e) => {
                        if (e.key === 'Enter') {
                          addItemToCube(dimension.id, e.currentTarget.value);
                          e.currentTarget.value = '';
                        }
                      }}
                    />
                    <Button
                      size="sm"
                      variant="outline"
                      className="h-6 w-6 p-0"
                      onClick={(e) => {
                        const input = e.currentTarget.previousElementSibling as HTMLInputElement;
                        addItemToCube(dimension.id, input.value);
                        input.value = '';
                      }}
                    >
                      <Plus className="w-3 h-3" />
                    </Button>
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>
        </CardContent>
      </Card>

      {/*  */}
      <div className="flex gap-2">
        <Button onClick={smartRandomGenerate} variant="outline">
          <Shuffle className="w-4 h-4 mr-2" />
          
        </Button>
        <Button onClick={generateIdea} disabled={isGenerating}>
          <RefreshCw className={`w-4 h-4 mr-2 ${isGenerating ? 'animate-spin' : ''}`} />
          {isGenerating ? '...' : ''}
        </Button>
      </div>

      {/*  */}
      {currentContent && (
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              {currentContentType === 'video' ? <Video className="w-5 h-5" /> : <FileText className="w-5 h-5" />}
              {currentContentType === 'video' ? '' : ''}
            </CardTitle>
          </CardHeader>
          <CardContent>
            <Tabs defaultValue="content" className="w-full">
              <TabsList className="grid w-full grid-cols-2">
                <TabsTrigger value="content"></TabsTrigger>
                {currentContentType === 'video' && (
                  <TabsTrigger value="script"></TabsTrigger>
                )}
              </TabsList>

              <TabsContent value="content" className="space-y-4">
                <Textarea
                  value={currentContent}
                  readOnly
                  className="min-h-[300px] font-mono text-sm"
                />
                <div className="flex gap-2">
                  <Button onClick={() => copyIdea(currentContent)} variant="outline">
                    <Copy className="w-4 h-4 mr-2" />
                    
                  </Button>
                  <Button onClick={saveIdea}>
                    <Save className="w-4 h-4 mr-2" />
                    
                  </Button>
                  {currentContentType === 'video' && (
                    <Button onClick={exportToExcel} variant="outline">
                      <Download className="w-4 h-4 mr-2" />
                      Excel
                    </Button>
                  )}
                </div>
              </TabsContent>

              {currentContentType === 'video' && (
                <TabsContent value="script" className="space-y-4">
                  <div className="space-y-4">
                    {videoScript.map((scene, index) => (
                      <Card key={index} className="border">
                        <CardHeader className="pb-2">
                          <CardTitle className="text-sm flex items-center gap-2">
                            <Camera className="w-4 h-4" />
                            {scene.sceneNumber}
                          </CardTitle>
                        </CardHeader>
                        <CardContent className="space-y-2">
                          <div className="grid grid-cols-2 gap-4 text-sm">
                            <div>
                              <strong></strong> {scene.sceneDescription}
                            </div>
                            <div>
                              <strong></strong> {scene.dialogue}
                            </div>
                            <div>
                              <strong></strong> {scene.tone}
                            </div>
                            <div>
                              <strong></strong> {scene.emotion}
                            </div>
                            <div>
                              <strong></strong> {scene.bgm}
                            </div>
                            <div>
                              <strong></strong> {scene.soundEffect}
                            </div>
                            <div>
                              <strong></strong> {scene.shotType}
                            </div>
                            <div>
                              <strong></strong> {scene.duration}
                            </div>
                          </div>
                        </CardContent>
                      </Card>
                    ))}
                  </div>
                </TabsContent>
              )}
            </Tabs>
          </CardContent>
        </Card>
      )}

      {/*  */}
      {generatedIdeas.length > 0 && (
        <Card>
          <CardHeader>
            <CardTitle></CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              {generatedIdeas.map((idea) => (
                <Card key={idea.id} className="border">
                  <CardContent className="p-4">
                    <div className="flex items-start justify-between">
                      <div className="flex-1">
                        <div className="flex items-center gap-2 mb-2">
                          <Badge variant="outline" className="text-xs">
                            {idea.contentType === 'video' ? '' : ''}
                          </Badge>
                          {idea.tags.map((tag, index) => (
                            <Badge key={index} variant="outline" className="text-xs">
                              {tag}
                            </Badge>
                          ))}
                        </div>
                        <p className="text-sm text-gray-600 mb-2">
                          {idea.generatedContent.substring(0, 200)}...
                        </p>
                        <p className="text-xs text-gray-500">
                          {new Date(idea.timestamp).toLocaleString()}
                        </p>
                      </div>
                      <div className="flex gap-1">
                        <Button size="sm" variant="outline" onClick={() => copyIdea(idea.generatedContent)}>
                          <Copy className="w-3 h-3" />
                        </Button>
                        <Button size="sm" variant="outline" onClick={saveIdea}>
                          <Save className="w-3 h-3" />
                        </Button>
                        {idea.contentType === 'video' && (
                          <Button size="sm" variant="outline" onClick={exportToExcel}>
                            <Download className="w-3 h-3" />
                          </Button>
                        )}
                      </div>
                    </div>
                  </CardContent>
                </Card>
              ))}
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  );
} 