/**
 * 
 * 
 */

import React, { useState } from 'react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Badge } from '@/components/ui/badge';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { useToast } from '@/hooks/use-toast';
import { useAuth } from '@/contexts/AuthContext';
import { useUserStore } from '@/store/userStore';
import { User, Settings, LogOut, Edit, Save, X, Camera, Shield, ArrowLeft, Gift } from 'lucide-react';
import { AvatarUpload } from '@/components/ui/avatar-upload';
import { NicknameSelector } from '@/components/ui/nickname-selector';
import { ContactVerification } from '@/components/ui/contact-verification';
import { useNavigate } from 'react-router-dom';

/**
 * 
 * @returns React 
 */
export default function ProfilePage() {
  const [isEditing, setIsEditing] = useState(false);
  const [editForm, setEditForm] = useState({
    nickname: '',
    email: '',
    phone: '',
    avatar: '',
  });
  const { toast } = useToast();
  const { user, isAuthenticated, logout } = useAuth();
  const { usageRemaining, userInviteStats } = useUserStore();
  const navigate = useNavigate();

  // 
  React.useEffect(() => {
    if (user) {
      setEditForm({
        nickname: user.nickname || '',
        email: user.email || '',
        phone: user.phone || '',
        avatar: user.avatar || '',
      });
    }
  }, [user]);

  /**
   * 
   */
  const handleEditChange = (field: string, value: string) => {
    setEditForm(prev => ({ ...prev, [field]: value }));
  };

  /**
   * 
   */
  const handleAvatarChange = (avatarUrl: string) => {
    setEditForm(prev => ({ ...prev, avatar: avatarUrl }));
  };

  /**
   * 
   */
  const handleNicknameChange = (nickname: string) => {
    setEditForm(prev => ({ ...prev, nickname }));
  };

  /**
   * 
   */
  const handlePhoneChange = (phone: string) => {
    setEditForm(prev => ({ ...prev, phone }));
  };

  /**
   * 
   */
  const handleEmailChange = (email: string) => {
    setEditForm(prev => ({ ...prev, email }));
  };

  /**
   * 
   */
  const handleSave = () => {
    //  API 
    toast({
      title: "",
      description: "",
    });
    setIsEditing(false);
  };

  /**
   * 
   */
  const handleCancel = () => {
    if (user) {
      setEditForm({
        nickname: user.nickname || '',
        email: user.email || '',
        phone: user.phone || '',
        avatar: user.avatar || '',
      });
    }
    setIsEditing(false);
  };

  /**
   * 
   */
  const handleLogout = async () => {
    try {
      await logout();
      // 
    } catch (err) {
      console.error(':', err);
      toast({
        title: "",
        description: "",
        variant: "destructive"
      });
    }
  };

  if (!isAuthenticated || !user) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto"></div>
          <p className="mt-4 text-gray-600">...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="container mx-auto px-4 py-8">
      <div className="mb-8">
        <div className="flex items-center gap-4 mb-4">
          <Button
            variant="ghost"
            onClick={() => navigate('/')}
            className="flex items-center gap-2 text-gray-600 hover:text-gray-800"
          >
            <ArrowLeft className="h-4 w-4" />
            
          </Button>
        </div>
        <h1 className="text-3xl font-bold text-gray-900 mb-2"></h1>
        <p className="text-gray-600">
          
        </p>
      </div>

      <Tabs defaultValue="profile" className="space-y-6">
        <TabsList className="grid w-full grid-cols-4">
          <TabsTrigger value="profile"></TabsTrigger>
          <TabsTrigger value="avatar"></TabsTrigger>
          <TabsTrigger value="nickname"></TabsTrigger>
          <TabsTrigger value="contact"></TabsTrigger>
        </TabsList>

        {/*  */}
        <TabsContent value="profile" className="space-y-6">
          <div className="grid gap-6 md:grid-cols-2">
            <Card>
              <CardHeader>
                <CardTitle className="flex items-center gap-2">
                  <User className="w-5 h-5" />
                  
                </CardTitle>
                <CardDescription>
                  
                </CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="flex items-center gap-3">
                  <div className="w-12 h-12 bg-blue-500 rounded-full flex items-center justify-center text-white font-semibold">
                    {user.nickname?.charAt(0) || user.username?.charAt(0) || user.email?.charAt(0) || 'U'}
                  </div>
                  <div>
                    <h3 className="font-medium">{user.nickname || user.username || ''}</h3>
                    <p className="text-sm text-gray-500">{user.email || ''}</p>
                  </div>
                </div>
                <div className="space-y-2">
                  <div className="flex justify-between">
                    <span className="text-sm text-gray-600">ID</span>
                    <span className="text-sm font-mono">{user.id}</span>
                  </div>
                  {user.phone && (
                    <div className="flex justify-between">
                      <span className="text-sm text-gray-600"></span>
                      <span className="text-sm">{user.phone}</span>
                    </div>
                  )}
                </div>
              </CardContent>
            </Card>

            {/*  */}
            <Card>
              <CardHeader>
                <CardTitle className="flex items-center gap-2">
                  <Settings className="w-5 h-5" />
                  
                </CardTitle>
                <CardDescription>
                  
                </CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="grid grid-cols-2 gap-4">
                  <div className="text-center p-4 bg-blue-50 rounded-lg">
                    <div className="text-2xl font-bold text-blue-600">{usageRemaining}</div>
                    <div className="text-sm text-gray-600"></div>
                  </div>
                  <div className="text-center p-4 bg-green-50 rounded-lg">
                    <div className="text-2xl font-bold text-green-600">{userInviteStats.totalRegistrations}</div>
                    <div className="text-sm text-gray-600"></div>
                  </div>
                </div>
                <div className="space-y-2">
                  <div className="flex justify-between">
                    <span className="text-sm text-gray-600"></span>
                    <Badge variant="secondary">{userInviteStats.totalRegistrations * 20}</Badge>
                  </div>
                </div>
              </CardContent>
            </Card>
          </div>

          {/*  */}
          <Card>
            <CardHeader>
              <CardTitle></CardTitle>
              <CardDescription>
                
              </CardDescription>
            </CardHeader>
            <CardContent>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <Button
                  variant="outline"
                  onClick={() => window.location.href = '/invite'}
                  className="justify-start"
                >
                  <User className="w-4 h-4 mr-2" />
                  
                </Button>
                <Button
                  variant="outline"
                  onClick={() => window.location.href = '/brand-library'}
                  className="justify-start"
                >
                  <Settings className="w-4 h-4 mr-2" />
                  
                </Button>
                <Button
                  variant="outline"
                  onClick={() => window.location.href = '/history'}
                  className="justify-start"
                >
                  <Settings className="w-4 h-4 mr-2" />
                  
                </Button>
                <Button
                  variant="destructive"
                  onClick={handleLogout}
                  className="justify-start"
                >
                  <LogOut className="w-4 h-4 mr-2" />
                  
                </Button>
              </div>
            </CardContent>
          </Card>
        </TabsContent>

        {/*  */}
        <TabsContent value="avatar" className="space-y-6">
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <Camera className="w-5 h-5" />
                
              </CardTitle>
              <CardDescription>
                
              </CardDescription>
            </CardHeader>
            <CardContent>
              <AvatarUpload
                currentAvatar={editForm.avatar}
                nickname={editForm.nickname || user?.nickname || ''}
                size="lg"
                onAvatarChange={handleAvatarChange}
                disabled={false}
              />
            </CardContent>
          </Card>
        </TabsContent>

        {/*  */}
        <TabsContent value="nickname" className="space-y-6">
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <User className="w-5 h-5" />
                
              </CardTitle>
              <CardDescription>
                
              </CardDescription>
            </CardHeader>
            <CardContent>
              <NicknameSelector
                currentNickname={editForm.nickname}
                onNicknameChange={handleNicknameChange}
                disabled={false}
              />
            </CardContent>
          </Card>
        </TabsContent>

        {/*  */}
        <TabsContent value="contact" className="space-y-6">
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <Shield className="w-5 h-5" />
                
              </CardTitle>
              <CardDescription>
                10
              </CardDescription>
            </CardHeader>
            <CardContent>
              <ContactVerification
                currentPhone={editForm.phone}
                currentEmail={editForm.email}
                onPhoneChange={handlePhoneChange}
                onEmailChange={handleEmailChange}
                disabled={false}
              />
            </CardContent>
          </Card>
          
          {/*  */}
          {!user.email && (
            <Card className="border-amber-200 bg-amber-50">
              <CardHeader>
                <CardTitle className="flex items-center gap-2 text-amber-800">
                  <Gift className="w-5 h-5" />
                  
                </CardTitle>
                <CardDescription className="text-amber-700">
                  10
                </CardDescription>
              </CardHeader>
              <CardContent>
                <div className="flex items-center justify-between">
                  <div className="text-sm text-amber-700">
                    <p> </p>
                    <p> 10</p>
                    <p> </p>
                  </div>
                  <Badge variant="outline" className="bg-amber-100 text-amber-800 border-amber-300">
                    +10
                  </Badge>
                </div>
              </CardContent>
            </Card>
          )}
        </TabsContent>
      </Tabs>
    </div>
  );
} 