import React, { useState, useEffect } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Badge } from '@/components/ui/badge';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Phone, Mail, Shield, CheckCircle, AlertCircle } from 'lucide-react';
import { useToast } from '@/hooks/use-toast';

/**
 * 
 */
interface ContactVerificationProps {
  /**  */
  currentPhone?: string;
  /**  */
  currentEmail?: string;
  /**  */
  onPhoneChange?: (phone: string) => void;
  /**  */
  onEmailChange?: (email: string) => void;
  /**  */
  disabled?: boolean;
}

/**
 * 
 */
export function ContactVerification({
  currentPhone = '',
  currentEmail = '',
  onPhoneChange,
  onEmailChange,
  disabled = false
}: ContactVerificationProps) {
  const [phone, setPhone] = useState(currentPhone);
  const [email, setEmail] = useState(currentEmail);
  const [phoneCode, setPhoneCode] = useState('');
  const [phoneVerified, setPhoneVerified] = useState(!!currentPhone);
  const [emailVerified, setEmailVerified] = useState(!!currentEmail);
  const [phoneCountdown, setPhoneCountdown] = useState(0);
  const [isSendingPhoneCode, setIsSendingPhoneCode] = useState(false);
  const [isVerifyingPhone, setIsVerifyingPhone] = useState(false);
  const [isVerifyingEmail, setIsVerifyingEmail] = useState(false);
  const { toast } = useToast();

  /**
   * 
   */
  useEffect(() => {
    if (phoneCountdown > 0) {
      const timer = setTimeout(() => setPhoneCountdown(phoneCountdown - 1), 1000);
      return () => clearTimeout(timer);
    }
  }, [phoneCountdown]);

  /**
   * 
   */
  const validatePhone = (phoneNumber: string): boolean => {
    const phoneRegex = /^1[3-9]\d{9}$/;
    return phoneRegex.test(phoneNumber);
  };

  /**
   * 
   */
  const validateEmail = (emailAddress: string): boolean => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return emailRegex.test(emailAddress);
  };

  /**
   * 
   */
  const sendPhoneCode = async () => {
    if (!validatePhone(phone)) {
      toast({
        title: "",
        description: "",
        variant: "destructive"
      });
      return;
    }

    setIsSendingPhoneCode(true);
    try {
      // API
      await new Promise(resolve => setTimeout(resolve, 1000)); // API
      
      setPhoneCountdown(60);
      toast({
        title: "",
        description: "",
      });
    } catch (error) {
      console.error(':', error);
      toast({
        title: "",
        description: "",
        variant: "destructive"
      });
    } finally {
      setIsSendingPhoneCode(false);
    }
  };

  /**
   * 
   */
  const verifyPhoneCode = async () => {
    if (!phoneCode.trim()) {
      toast({
        title: "",
        description: "",
        variant: "destructive"
      });
      return;
    }

    setIsVerifyingPhone(true);
    try {
      // API
      await new Promise(resolve => setTimeout(resolve, 1000)); // API
      
      // API
      if (phoneCode === '123456') {
        setPhoneVerified(true);
        onPhoneChange?.(phone);
        toast({
          title: "",
          description: "",
        });
      } else {
        toast({
          title: "",
          description: "",
          variant: "destructive"
        });
      }
    } catch (error) {
      console.error(':', error);
      toast({
        title: "",
        description: "",
        variant: "destructive"
      });
    } finally {
      setIsVerifyingPhone(false);
    }
  };

  /**
   * 
   */
  const verifyEmail = async () => {
    if (!validateEmail(email)) {
      toast({
        title: "",
        description: "",
        variant: "destructive"
      });
      return;
    }

    setIsVerifyingEmail(true);
    try {
      // API
      await new Promise(resolve => setTimeout(resolve, 1000)); // API
      
      // API
      setEmailVerified(true);
      onEmailChange?.(email);
      toast({
        title: "",
        description: "",
      });
    } catch (error) {
      console.error(':', error);
      toast({
        title: "",
        description: "",
        variant: "destructive"
      });
    } finally {
      setIsVerifyingEmail(false);
    }
  };

  return (
    <div className="space-y-6">
      {/*  */}
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <Phone className="h-5 w-5" />
            
            {phoneVerified && (
              <Badge variant="secondary" className="bg-green-100 text-green-700">
                <CheckCircle className="h-3 w-3 mr-1" />
                
              </Badge>
            )}
          </CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="space-y-2">
            <Label htmlFor="phone"></Label>
            <div className="flex gap-2">
              <Input
                id="phone"
                type="tel"
                value={phone}
                onChange={(e) => setPhone(e.target.value)}
                placeholder=""
                disabled={disabled || phoneVerified}
                maxLength={11}
              />
              {!phoneVerified && (
                <Button
                  onClick={sendPhoneCode}
                  disabled={disabled || isSendingPhoneCode || phoneCountdown > 0 || !validatePhone(phone)}
                  size="sm"
                >
                  {isSendingPhoneCode ? '...' : phoneCountdown > 0 ? `${phoneCountdown}s` : ''}
                </Button>
              )}
            </div>
          </div>

          {!phoneVerified && (
            <div className="space-y-2">
              <Label htmlFor="phone-code"></Label>
              <div className="flex gap-2">
                <Input
                  id="phone-code"
                  value={phoneCode}
                  onChange={(e) => setPhoneCode(e.target.value)}
                  placeholder="6"
                  disabled={disabled}
                  maxLength={6}
                />
                <Button
                  onClick={verifyPhoneCode}
                  disabled={disabled || isVerifyingPhone || !phoneCode.trim()}
                  size="sm"
                >
                  {isVerifyingPhone ? '...' : ''}
                </Button>
              </div>
            </div>
          )}

          {phoneVerified && (
            <div className="flex items-center gap-2 p-3 bg-green-50 rounded-lg">
              <CheckCircle className="h-4 w-4 text-green-600" />
              <span className="text-sm text-green-700">{phone}</span>
            </div>
          )}
        </CardContent>
      </Card>

      {/*  */}
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <Mail className="h-5 w-5" />
            
            {emailVerified && (
              <Badge variant="secondary" className="bg-green-100 text-green-700">
                <CheckCircle className="h-3 w-3 mr-1" />
                
              </Badge>
            )}
          </CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="space-y-2">
            <Label htmlFor="email"></Label>
            <div className="flex gap-2">
              <Input
                id="email"
                type="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                placeholder=""
                disabled={disabled || emailVerified}
              />
              {!emailVerified && (
                <Button
                  onClick={verifyEmail}
                  disabled={disabled || isVerifyingEmail || !validateEmail(email)}
                  size="sm"
                >
                  {isVerifyingEmail ? '...' : ''}
                </Button>
              )}
            </div>
          </div>

          {emailVerified && (
            <div className="flex items-center gap-2 p-3 bg-green-50 rounded-lg">
              <CheckCircle className="h-4 w-4 text-green-600" />
              <span className="text-sm text-green-700">{email}</span>
            </div>
          )}
        </CardContent>
      </Card>

      {/*  */}
      <div className="text-xs text-gray-500 space-y-1">
        <p> 5</p>
        <p> 123456</p>
        <p> </p>
      </div>
    </div>
  );
} 