/**
 * 
 *  MarkdownJSONHTML
 */

import React, { useState, useRef } from 'react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Input } from '@/components/ui/input';
import { Textarea } from '@/components/ui/textarea';
import { Label } from '@/components/ui/label';
import { 
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select';
import { 
  FileText,
  Globe,
  Image,
  Download,
  Upload,
  Copy,
  Search,
  File,
  Code,
  BookOpen,
  CheckCircle,
  AlertCircle,
  Info,
  Loader2
} from 'lucide-react';
import { useToast } from '@/hooks/use-toast';

/**
 * 
 */
interface ExtractResult {
  id: string;
  source: string;
  sourceType: 'url' | 'file' | 'text';
  contentType: 'markdown' | 'json' | 'html' | 'image' | 'webpage';
  content: string;
  metadata?: {
    title?: string;
    description?: string;
    keywords?: string[];
    author?: string;
    date?: string;
    wordCount?: number;
    charCount?: number;
  };
  extractedAt: string;
  status: 'success' | 'error' | 'processing';
  error?: string;
}

/**
 * 
 * @returns React 
 */
export function ContentExtractor() {
  const { toast } = useToast();
  const fileInputRef = useRef<HTMLInputElement>(null);
  
  // 
  const [extractMethod, setExtractMethod] = useState<'url' | 'file' | 'text'>('url');
  const [contentType, setContentType] = useState<'markdown' | 'json' | 'html' | 'image' | 'webpage'>('webpage');
  const [url, setUrl] = useState('');
  const [text, setText] = useState('');
  const [extractResults, setExtractResults] = useState<ExtractResult[]>([]);
  const [isExtracting, setIsExtracting] = useState(false);
  const [selectedFile, setSelectedFile] = useState<File | null>(null);

  /**
   * URL
   */
  const extractFromUrl = async () => {
    if (!url.trim()) {
      toast({
        title: "URL",
        description: "",
        variant: "destructive"
      });
      return;
    }

    setIsExtracting(true);
    const resultId = Date.now().toString();

    try {
      // API
      await new Promise(resolve => setTimeout(resolve, 2000));
      
      // 
      const mockResult: ExtractResult = {
        id: resultId,
        source: url,
        sourceType: 'url',
        contentType: contentType,
        content: `# 

## 

 ${url} 

### 
- 2024215
- 
- 

### 


### 
1. ****
2. ****
3. ****

### 
- 
- 
- 
- 

> `,
        metadata: {
          title: '',
          description: '',
          keywords: ['', '', ''],
          author: '',
          date: '2024-02-15',
          wordCount: 150,
          charCount: 800
        },
        extractedAt: new Date().toISOString(),
        status: 'success'
      };

      setExtractResults(prev => [mockResult, ...prev]);
      
      toast({
        title: "",
        description: "",
      });
    } catch {
      const errorResult: ExtractResult = {
        id: resultId,
        source: url,
        sourceType: 'url',
        contentType: contentType,
        content: '',
        extractedAt: new Date().toISOString(),
        status: 'error',
        error: 'URL'
      };
      
      setExtractResults(prev => [errorResult, ...prev]);
      
      toast({
        title: "",
        description: "URL",
        variant: "destructive"
      });
    } finally {
      setIsExtracting(false);
    }
  };

  /**
   * 
   */
  const extractFromFile = async () => {
    if (!selectedFile) {
      toast({
        title: "",
        description: "",
        variant: "destructive"
      });
      return;
    }

    setIsExtracting(true);
    const resultId = Date.now().toString();

    try {
      // 
      await new Promise(resolve => setTimeout(resolve, 1500));
      
      const fileContent = await readFileContent(selectedFile);
      
      const mockResult: ExtractResult = {
        id: resultId,
        source: selectedFile.name,
        sourceType: 'file',
        contentType: contentType,
        content: fileContent,
        metadata: {
          title: selectedFile.name,
          description: ` ${selectedFile.name} `,
          wordCount: fileContent.split(/\s+/).length,
          charCount: fileContent.length
        },
        extractedAt: new Date().toISOString(),
        status: 'success'
      };

      setExtractResults(prev => [mockResult, ...prev]);
      
      toast({
        title: "",
        description: ` ${selectedFile.name} `,
      });
    } catch {
      const errorResult: ExtractResult = {
        id: resultId,
        source: selectedFile.name,
        sourceType: 'file',
        contentType: contentType,
        content: '',
        extractedAt: new Date().toISOString(),
        status: 'error',
        error: ''
      };
      
      setExtractResults(prev => [errorResult, ...prev]);
      
      toast({
        title: "",
        description: "",
        variant: "destructive"
      });
    } finally {
      setIsExtracting(false);
    }
  };

  /**
   * 
   */
  const extractFromText = async () => {
    if (!text.trim()) {
      toast({
        title: "",
        description: "",
        variant: "destructive"
      });
      return;
    }

    setIsExtracting(true);
    const resultId = Date.now().toString();

    try {
      // 
      await new Promise(resolve => setTimeout(resolve, 1000));
      
      const mockResult: ExtractResult = {
        id: resultId,
        source: '',
        sourceType: 'text',
        contentType: contentType,
        content: text,
        metadata: {
          title: '',
          description: '',
          wordCount: text.split(/\s+/).length,
          charCount: text.length
        },
        extractedAt: new Date().toISOString(),
        status: 'success'
      };

      setExtractResults(prev => [mockResult, ...prev]);
      
      toast({
        title: "",
        description: "",
      });
    } catch {
      const errorResult: ExtractResult = {
        id: resultId,
        source: '',
        sourceType: 'text',
        contentType: contentType,
        content: '',
        extractedAt: new Date().toISOString(),
        status: 'error',
        error: ''
      };
      
      setExtractResults(prev => [errorResult, ...prev]);
      
      toast({
        title: "",
        description: "",
        variant: "destructive"
      });
    } finally {
      setIsExtracting(false);
    }
  };

  /**
   * 
   */
  const readFileContent = (file: File): Promise<string> => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      
      reader.onload = (e) => {
        const content = e.target?.result as string;
        resolve(content);
      };
      
      reader.onerror = () => {
        reject(new Error(''));
      };
      
      if (file.type.includes('text') || file.name.endsWith('.md') || file.name.endsWith('.json')) {
        reader.readAsText(file);
      } else {
        reader.readAsDataURL(file);
      }
    });
  };

  /**
   * 
   */
  const handleFileSelect = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (file) {
      setSelectedFile(file);
      
      // 
      if (file.name.endsWith('.md')) {
        setContentType('markdown');
      } else if (file.name.endsWith('.json')) {
        setContentType('json');
      } else if (file.name.endsWith('.html') || file.name.endsWith('.htm')) {
        setContentType('html');
      } else if (file.type.startsWith('image/')) {
        setContentType('image');
      }
    }
  };

  /**
   * 
   */
  const copyResult = (content: string) => {
    navigator.clipboard.writeText(content);
    toast({
      title: "",
      description: "",
    });
  };

  /**
   * 
   */
  const downloadResult = (result: ExtractResult) => {
    const blob = new Blob([result.content], { type: 'text/plain' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `extracted-content-${result.id}.txt`;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
    
    toast({
      title: "",
      description: "",
    });
  };

  /**
   * 
   */
  const formatTime = (timeString: string) => {
    return new Date(timeString).toLocaleString('zh-CN');
  };

  /**
   * 
   */
  const getContentTypeIcon = (type: string) => {
    switch (type) {
      case 'markdown':
        return <FileText className="w-4 h-4" />;
      case 'json':
        return <Code className="w-4 h-4" />;
      case 'html':
        return <File className="w-4 h-4" />;
      case 'image':
        return <Image className="w-4 h-4" />;
      case 'webpage':
        return <Globe className="w-4 h-4" />;
      default:
        return <FileText className="w-4 h-4" />;
    }
  };

  /**
   * 
   */
  const getStatusIcon = (status: string) => {
    switch (status) {
      case 'success':
        return <CheckCircle className="w-4 h-4 text-green-500" />;
      case 'error':
        return <AlertCircle className="w-4 h-4 text-red-500" />;
      case 'processing':
        return <Loader2 className="w-4 h-4 text-blue-500 animate-spin" />;
      default:
        return <Info className="w-4 h-4 text-gray-500" />;
    }
  };

  return (
    <div className="space-y-6">
      {/*  */}
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <Search className="w-5 h-5" />
            
          </CardTitle>
          <CardDescription>
            
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          {/*  */}
          <div className="grid grid-cols-3 gap-4">
            <div>
              <Label></Label>
              <Select value={extractMethod} onValueChange={(value: 'url' | 'file' | 'text') => setExtractMethod(value)}>
                <SelectTrigger>
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="url">
                    <div className="flex items-center gap-2">
                      <Globe className="w-4 h-4" />
                      URL
                    </div>
                  </SelectItem>
                  <SelectItem value="file">
                    <div className="flex items-center gap-2">
                      <Upload className="w-4 h-4" />
                      
                    </div>
                  </SelectItem>
                  <SelectItem value="text">
                    <div className="flex items-center gap-2">
                      <FileText className="w-4 h-4" />
                      
                    </div>
                  </SelectItem>
                </SelectContent>
              </Select>
            </div>

            <div>
              <Label></Label>
              <Select value={contentType} onValueChange={(value: 'markdown' | 'json' | 'html' | 'image' | 'webpage') => setContentType(value)}>
                <SelectTrigger>
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="webpage"></SelectItem>
                  <SelectItem value="markdown">Markdown</SelectItem>
                  <SelectItem value="json">JSON</SelectItem>
                  <SelectItem value="html">HTML</SelectItem>
                  <SelectItem value="image"></SelectItem>
                </SelectContent>
              </Select>
            </div>

            <div className="flex items-end">
              <Button 
                onClick={() => {
                  if (extractMethod === 'url') {
                    extractFromUrl();
                  } else if (extractMethod === 'file') {
                    extractFromFile();
                  } else {
                    extractFromText();
                  }
                }}
                disabled={isExtracting}
                className="w-full"
              >
                {isExtracting ? (
                  <>
                    <Loader2 className="w-4 h-4 mr-2 animate-spin" />
                    ...
                  </>
                ) : (
                  <>
                    <Search className="w-4 h-4 mr-2" />
                    
                  </>
                )}
              </Button>
            </div>
          </div>

          {/*  */}
          {extractMethod === 'url' && (
            <div>
              <Label>URL</Label>
              <div className="flex gap-2">
                <Input
                  placeholder="https://example.com"
                  value={url}
                  onChange={(e) => setUrl(e.target.value)}
                  className="flex-1"
                />
                <Button variant="outline" onClick={() => setUrl('')}>
                  
                </Button>
              </div>
            </div>
          )}

          {extractMethod === 'file' && (
            <div>
              <Label></Label>
              <div className="flex gap-2">
                <Input
                  ref={fileInputRef}
                  type="file"
                  onChange={handleFileSelect}
                  accept=".md,.json,.html,.htm,.txt,image/*"
                  className="flex-1"
                />
                <Button 
                  variant="outline" 
                  onClick={() => fileInputRef.current?.click()}
                >
                  <Upload className="w-4 h-4 mr-2" />
                  
                </Button>
              </div>
              {selectedFile && (
                <div className="mt-2 p-2 bg-gray-50 rounded">
                  <div className="flex items-center gap-2">
                    <File className="w-4 h-4" />
                    <span className="text-sm">{selectedFile.name}</span>
                    <span className="text-xs text-gray-500">
                      ({(selectedFile.size / 1024).toFixed(1)} KB)
                    </span>
                  </div>
                </div>
              )}
            </div>
          )}

          {extractMethod === 'text' && (
            <div>
              <Label></Label>
              <Textarea
                placeholder="..."
                value={text}
                onChange={(e) => setText(e.target.value)}
                rows={6}
              />
            </div>
          )}
        </CardContent>
      </Card>

      {/*  */}
      {extractResults.length > 0 && (
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <BookOpen className="w-5 h-5" />
               ({extractResults.length})
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              {extractResults.map((result) => (
                <Card key={result.id} className="border">
                  <CardContent className="p-4">
                    <div className="flex items-start justify-between mb-3">
                      <div className="flex items-center gap-2">
                        {getContentTypeIcon(result.contentType)}
                        <span className="font-medium">{result.source}</span>
                        <Badge variant="outline">
                          {result.contentType}
                        </Badge>
                        {getStatusIcon(result.status)}
                      </div>
                      <div className="flex gap-1">
                        <Button
                          size="sm"
                          variant="outline"
                          onClick={() => copyResult(result.content)}
                        >
                          <Copy className="w-4 h-4" />
                        </Button>
                        <Button
                          size="sm"
                          variant="outline"
                          onClick={() => downloadResult(result)}
                        >
                          <Download className="w-4 h-4" />
                        </Button>
                      </div>
                    </div>

                    {result.status === 'success' ? (
                      <div>
                        {result.metadata && (
                          <div className="mb-3 p-2 bg-gray-50 rounded text-sm">
                            <div className="grid grid-cols-2 gap-2">
                              {result.metadata.title && (
                                <div><strong></strong>{result.metadata.title}</div>
                              )}
                              {result.metadata.wordCount && (
                                <div><strong></strong>{result.metadata.wordCount}</div>
                              )}
                              {result.metadata.charCount && (
                                <div><strong></strong>{result.metadata.charCount}</div>
                              )}
                              {result.metadata.date && (
                                <div><strong></strong>{result.metadata.date}</div>
                              )}
                            </div>
                          </div>
                        )}
                        
                        <div className="max-h-60 overflow-y-auto">
                          <pre className="text-sm bg-gray-50 p-3 rounded whitespace-pre-wrap">
                            {result.content}
                          </pre>
                        </div>
                      </div>
                    ) : (
                      <div className="text-red-500">
                        {result.error || ''}
                      </div>
                    )}

                    <div className="mt-2 text-xs text-gray-500">
                      {formatTime(result.extractedAt)}
                    </div>
                  </CardContent>
                </Card>
              ))}
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  );
} 