/**
 * 
 * +
 */

import React, { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { useToast } from '@/hooks/use-toast';
import { useAuth } from '@/contexts/AuthContext';
import { useNavigate, Link } from 'react-router-dom';
import { ArrowLeft, Eye, EyeOff, Phone, Mail, Lock, User, Send } from 'lucide-react';
import { Checkbox } from '@/components/ui/checkbox';

/**
 * 
 * @returns React 
 */
export default function LoginPage() {
  const [activeTab, setActiveTab] = useState('password');
  const [showPassword, setShowPassword] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [countdown, setCountdown] = useState(0);
  const [rememberPassword, setRememberPassword] = useState(false);
  
  // 
  const [passwordForm, setPasswordForm] = useState({
    phone: '',
    password: ''
  });
  
  // 
  const [codeForm, setCodeForm] = useState({
    phone: '',
    code: ''
  });
  
  const { toast } = useToast();
  const { login } = useAuth();
  const navigate = useNavigate();

  /**
   * 
   */
  const handlePasswordFormChange = (field: string, value: string) => {
    setPasswordForm(prev => ({ ...prev, [field]: value }));
  };

  /**
   * 
   */
  const handleCodeFormChange = (field: string, value: string) => {
    setCodeForm(prev => ({ ...prev, [field]: value }));
  };

  /**
   * 
   */
  const sendCode = async () => {
    if (countdown > 0) return;
    
    if (!codeForm.phone) {
      toast({
        title: "",
        variant: "destructive"
      });
      return;
    }
    
    if (!/^1[3-9]\d{9}$/.test(codeForm.phone)) {
      toast({
        title: "",
        variant: "destructive"
      });
      return;
    }
    
    try {
      // API
      toast({
        title: "",
        description: ""
      });
      
      // 
      setCountdown(60);
      const timer = setInterval(() => {
        setCountdown(prev => {
          if (prev <= 1) {
            clearInterval(timer);
            return 0;
          }
          return prev - 1;
        });
      }, 1000);
    } catch (error) {
      toast({
        title: "",
        description: "",
        variant: "destructive"
      });
    }
  };

  /**
   * 
   */
  const handlePasswordLogin = async () => {
    if (!passwordForm.phone || !passwordForm.password) {
      toast({
        title: "",
        variant: "destructive"
      });
      return;
    }
    
    setIsLoading(true);
    try {
      await login(passwordForm.phone, passwordForm.password);
      
      toast({
        title: "",
        description: ""
      });
      
      // 
      const selectedPlan = localStorage.getItem("selectedPlan");
      if (selectedPlan) {
        navigate('/payment');
      } else {
        navigate('/');
      }
    } catch (error: any) {
      toast({
        title: "",
        description: error.message || "",
        variant: "destructive"
      });
    } finally {
      setIsLoading(false);
    }
  };

  /**
   * 
   */
  const handleCodeLogin = async () => {
    if (!codeForm.phone || !codeForm.code) {
      toast({
        title: "",
        variant: "destructive"
      });
      return;
    }
    
    setIsLoading(true);
    try {
      await login(codeForm.phone, codeForm.code);
      
      toast({
        title: "",
        description: ""
      });
      
      // 
      const selectedPlan = localStorage.getItem("selectedPlan");
      if (selectedPlan) {
        navigate('/payment');
      } else {
        navigate('/');
      }
    } catch (error: any) {
      toast({
        title: "",
        description: error.message || "",
        variant: "destructive"
      });
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-purple-50 flex items-center justify-center p-4">
      <div className="w-full max-w-md">
        {/*  */}
        <Button
          variant="ghost"
          onClick={() => navigate('/')}
          className="mb-6 flex items-center gap-2 text-gray-600 hover:text-gray-800"
        >
          <ArrowLeft className="h-4 w-4" />
          
        </Button>
        
        <Card className="shadow-xl">
          <CardHeader className="text-center">
            <CardTitle className="text-2xl font-bold"></CardTitle>
            <CardDescription>
              
            </CardDescription>
          </CardHeader>
          <CardContent>
            <Tabs value={activeTab} onValueChange={setActiveTab} className="w-full">
              <TabsList className="grid w-full grid-cols-2">
                <TabsTrigger value="password"></TabsTrigger>
                <TabsTrigger value="code"></TabsTrigger>
              </TabsList>
              
              {/*  */}
              <TabsContent value="password" className="space-y-4">
                <div className="space-y-2">
                  <Label htmlFor="password-phone"></Label>
                  <div className="relative">
                    <Phone className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                    <Input
                      id="password-phone"
                      type="tel"
                      placeholder=""
                      value={passwordForm.phone}
                      onChange={(e) => handlePasswordFormChange('phone', e.target.value)}
                      className="pl-10"
                    />
                  </div>
                </div>
                
                <div className="space-y-2">
                  <Label htmlFor="password-password"></Label>
                  <div className="relative">
                    <Lock className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                    <Input
                      id="password-password"
                      type={showPassword ? "text" : "password"}
                      placeholder=""
                      value={passwordForm.password}
                      onChange={(e) => handlePasswordFormChange('password', e.target.value)}
                      className="pl-10 pr-10"
                    />
                    <Button
                      type="button"
                      variant="ghost"
                      size="sm"
                      className="absolute right-0 top-0 h-full px-3 py-2 hover:bg-transparent"
                      onClick={() => setShowPassword(!showPassword)}
                    >
                      {showPassword ? <EyeOff className="h-4 w-4" /> : <Eye className="h-4 w-4" />}
                    </Button>
                  </div>
                </div>
                
                <div className="flex items-center justify-between">
                  <div className="flex items-center space-x-2">
                    <Checkbox
                      id="remember-password"
                      checked={rememberPassword}
                      onCheckedChange={(checked) => setRememberPassword(checked as boolean)}
                    />
                    <Label htmlFor="remember-password" className="text-sm">
                      
                    </Label>
                  </div>
                  <Link to="/forgot-password" className="text-sm text-blue-600 hover:underline">
                    
                  </Link>
                </div>
                
                <Button
                  onClick={handlePasswordLogin}
                  disabled={isLoading}
                  className="w-full"
                >
                  {isLoading ? "..." : ""}
                </Button>
              </TabsContent>
              
              {/*  */}
              <TabsContent value="code" className="space-y-4">
                <div className="space-y-2">
                  <Label htmlFor="code-phone"></Label>
                  <div className="relative">
                    <Phone className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                    <Input
                      id="code-phone"
                      type="tel"
                      placeholder=""
                      value={codeForm.phone}
                      onChange={(e) => handleCodeFormChange('phone', e.target.value)}
                      className="pl-10"
                    />
                  </div>
                </div>
                
                <div className="space-y-2">
                  <Label htmlFor="code-verification"></Label>
                  <div className="flex gap-2">
                    <div className="relative flex-1">
                      <Input
                        id="code-verification"
                        placeholder=""
                        value={codeForm.code}
                        onChange={(e) => handleCodeFormChange('code', e.target.value)}
                      />
                    </div>
                    <Button
                      type="button"
                      variant="outline"
                      onClick={sendCode}
                      disabled={countdown > 0}
                      className="w-24"
                    >
                      {countdown > 0 ? `${countdown}s` : ''}
                    </Button>
                  </div>
                </div>
                
                <Button
                  onClick={handleCodeLogin}
                  disabled={isLoading}
                  className="w-full"
                >
                  {isLoading ? "..." : ""}
                </Button>
              </TabsContent>
            </Tabs>
            
            {/*  */}
            <div className="mt-6 text-center">
              <p className="text-sm text-gray-600">
                
                <Link to="/register" className="text-blue-600 hover:underline ml-1">
                  
                </Link>
              </p>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  );
} 