import React from 'react';
import { useAuth } from '@/contexts/AuthContext';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';

export default function AuthTestPage() {
  const { 
    user, 
    isAuthenticated, 
    isLoading, 
    error, 
    showLogin, 
    logout
  } = useAuth();

  const handleShowLogin = async () => {
    console.log('AuthTestPage: showLogin called');
    try {
      await showLogin();
    } catch (error) {
      console.error('AuthTestPage: showLogin failed:', error);
    }
  };

  return (
    <div className="container mx-auto px-4 py-8">
      <Card>
        <CardHeader>
          <CardTitle></CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="grid grid-cols-2 gap-4 text-sm">
            <div>
              <strong>:</strong> {error || ''}
            </div>
            <div>
              <strong>:</strong> {isAuthenticated ? '' : ''}
            </div>
            <div>
              <strong>:</strong> {isLoading ? '' : ''}
            </div>
            <div>
              <strong>:</strong> 
            </div>
          </div>

          {user && (
            <div className="border rounded p-4">
              <h3 className="font-semibold mb-2">:</h3>
              <pre className="text-xs bg-gray-100 p-2 rounded">
                {JSON.stringify(user, null, 2)}
              </pre>
            </div>
          )}

          <div className="flex space-x-4">
            <Button 
              onClick={handleShowLogin} 
            >
              
            </Button>
            {isAuthenticated && (
              <Button onClick={logout} variant="outline">
                
              </Button>
            )}
          </div>

          <div className="text-xs text-gray-500">
            <p>:</p>
            <p>- ""</p>
            <p>-  Authing </p>
            <p>-  authing-js-sdk  AuthenticationClient</p>
            <p>- : </p>
          </div>
        </CardContent>
      </Card>
    </div>
  );
} 