import React, { useState, useRef } from 'react';
import { Button } from '@/components/ui/button';
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Upload, RefreshCw, Camera } from 'lucide-react';
import { useToast } from '@/hooks/use-toast';

/**
 * 
 */
interface AvatarUploadProps {
  /** URL */
  currentAvatar?: string;
  /**  */
  nickname?: string;
  /**  */
  size?: 'sm' | 'md' | 'lg';
  /**  */
  showUploadButton?: boolean;
  /**  */
  showRandomButton?: boolean;
  /**  */
  onAvatarChange?: (avatarUrl: string) => void;
  /**  */
  disabled?: boolean;
}

/**
 * 
 */
export function AvatarUpload({
  currentAvatar,
  nickname = '',
  size = 'md',
  showUploadButton = true,
  showRandomButton = true,
  onAvatarChange,
  disabled = false
}: AvatarUploadProps) {
  const [avatarUrl, setAvatarUrl] = useState(currentAvatar || '');
  const [isUploading, setIsUploading] = useState(false);
  const [isGenerating, setIsGenerating] = useState(false);
  const fileInputRef = useRef<HTMLInputElement>(null);
  const { toast } = useToast();

  /**
   * 
   */
  const getSizeClass = () => {
    switch (size) {
      case 'sm':
        return 'h-16 w-16';
      case 'lg':
        return 'h-32 w-32';
      default:
        return 'h-24 w-24';
    }
  };

  /**
   * 
   */
  const getButtonSize = () => {
    switch (size) {
      case 'sm':
        return 'sm';
      case 'lg':
        return 'lg';
      default:
        return 'default';
    }
  };

  /**
   * 
   */
  const generateRandomAvatar = async () => {
    setIsGenerating(true);
    try {
      //  Dicebear API 
      const seed = nickname + Date.now();
      const randomAvatarUrl = `https://api.dicebear.com/7.x/avataaars/svg?seed=${encodeURIComponent(seed)}`;
      
      setAvatarUrl(randomAvatarUrl);
      onAvatarChange?.(randomAvatarUrl);
      
      toast({
        title: "",
        description: "",
      });
    } catch (error) {
      console.error(':', error);
      toast({
        title: "",
        description: "",
        variant: "destructive"
      });
    } finally {
      setIsGenerating(false);
    }
  };

  /**
   * 
   */
  const handleFileUpload = async (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (!file) return;

    // 
    if (!file.type.startsWith('image/')) {
      toast({
        title: "",
        description: "",
        variant: "destructive"
      });
      return;
    }

    //  5MB
    if (file.size > 5 * 1024 * 1024) {
      toast({
        title: "",
        description: " 5MB",
        variant: "destructive"
      });
      return;
    }

    setIsUploading(true);
    try {
      //  URL
      const imageUrl = URL.createObjectURL(file);
      setAvatarUrl(imageUrl);
      onAvatarChange?.(imageUrl);
      
      toast({
        title: "",
        description: "",
      });
    } catch (error) {
      console.error(':', error);
      toast({
        title: "",
        description: "",
        variant: "destructive"
      });
    } finally {
      setIsUploading(false);
    }
  };

  /**
   * 
   */
  const triggerFileSelect = () => {
    fileInputRef.current?.click();
  };

  /**
   * 
   */
  const getInitials = () => {
    return nickname.charAt(0).toUpperCase();
  };

  return (
    <div className="flex flex-col items-center space-y-4">
      {/*  */}
      <div className="relative">
        <Avatar className={getSizeClass()}>
          <AvatarImage src={avatarUrl} alt={nickname} />
          <AvatarFallback className="bg-blue-500 text-white text-lg font-semibold">
            {getInitials()}
          </AvatarFallback>
        </Avatar>
        
        {/*  */}
        {(isUploading || isGenerating) && (
          <div className="absolute inset-0 bg-black/20 rounded-full flex items-center justify-center">
            <RefreshCw className="h-6 w-6 text-white animate-spin" />
          </div>
        )}
      </div>

      {/*  */}
      <div className="flex flex-col sm:flex-row gap-2 w-full max-w-xs">
        {showUploadButton && (
          <Button
            variant="outline"
            size={getButtonSize()}
            onClick={triggerFileSelect}
            disabled={disabled || isUploading || isGenerating}
            className="flex-1"
          >
            <Camera className="h-4 w-4 mr-2" />
            {isUploading ? '...' : ''}
          </Button>
        )}
        
        {showRandomButton && (
          <Button
            variant="outline"
            size={getButtonSize()}
            onClick={generateRandomAvatar}
            disabled={disabled || isUploading || isGenerating}
            className="flex-1"
          >
            <RefreshCw className="h-4 w-4 mr-2" />
            {isGenerating ? '...' : ''}
          </Button>
        )}
      </div>

      {/*  */}
      <Input
        ref={fileInputRef}
        type="file"
        accept="image/*"
        onChange={handleFileUpload}
        className="hidden"
      />

      {/*  */}
      <div className="text-xs text-gray-500 text-center">
        <p> JPGPNG  5MB</p>
        <p>""</p>
      </div>
    </div>
  );
} 